import {
    TODAY_STATISTICS,
    TOTAL_STATISTICS,
    LETEST_LEADS,
    REPLY_STATUS
} from "../actions/type";
const initialState = {
    TodayLeads: null,
    TotalLeads: null,
    LetestLeads: null,
    ReplyStatus: null
};
// eslint-disable-next-line
export default function (state = initialState, actions) {
    switch (actions.type) {
        case TOTAL_STATISTICS:
            return {
                ...state,
                TotalLeads: actions.payload
            };
        case TODAY_STATISTICS:
            return {
                ...state,
                TodayLeads: actions.payload
            };
        case LETEST_LEADS:
            return {
                ...state,
                LetestLeads: actions.payload
            };
        case REPLY_STATUS:
            return {
                ...state,
                ReplyStatus: actions.payload
            };

        default:
            return state;
    }
}
