import Axios from "axios";
import { ALL_REPLY, ADD_REPLY, EDIT_REPLY, REPLY_UPDATE_STATS, DELETE_REPLY } from "./type";
import { API } from "../../Global";

export const getallreply = () => dispatch => {
    Axios.post(API + "/allReply", { token: localStorage.getItem("auth-Token") })
        .then(res => {
            dispatch({
                type: ALL_REPLY,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const deleteReply = data => dispatch => {
    Axios.post(API + "/deleteReply", data)
        .then(res => {
            dispatch({
                type: DELETE_REPLY,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const editreply = newData => dispatch => {
    Axios.post(API + "/editReply", newData)
        .then(res => {
            dispatch({
                type: EDIT_REPLY,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const createReply = data => dispatch => {
    Axios.post(API + "/createReply", data)
        .then(res => {
            dispatch({
                type: ADD_REPLY,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const replystatus = () => dispatch => {
    dispatch({
        type: REPLY_UPDATE_STATS
    });
};
