import React, { Component } from "react";
import {
    DialogTitle,
    DialogContent,
    DialogContentText,
    TextField,
    Button,
    Dialog,
    DialogActions,
  
} from "@material-ui/core";
import { connect } from "react-redux";
import { editLink } from "../../../redux/actions/Link";
class LinkEditModel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            model: false,
            url: "",
            title: "",
            id: ""
        };
    }
    componentDidMount() {
        this.setState({
            id: this.props.data._id,
            url: this.props.data.url,
            title: this.props.data.urltext
        });
    }
    HandelSubmit() {
        const newData = {
            token: localStorage.getItem("auth-Token"),
            url: this.state.url,
            title: this.state.title,
            id: this.state.id
        };
        this.props.editLink(newData);
        this.setState({ model: false });
    }

    render() {
        return (
            <div>
                <div
                    onClick={() => {
                        this.setState({ model: true });
                    }}
                    className="icon mr-2 cursor-pointer text-xl"
                >
                    <i className="fas fa-edit "></i>
                </div>
                <Dialog
                    fullWidth={true}
                    maxWidth="xs"
                    open={this.state.model}
                    onClose={() => this.setState({ model: false })}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        {"Do you Want to change Your Offer Link ?"}
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            <div class="row ml-2">
                                <div className="col-sm-12">
                                    <TextField
                                        fullWidth
                                        id="filled-email-input"
                                        label="Link"
                                        className="mb-3"
                                        type="text"
                                        autoComplete="off"
                                        variant="standard"
                                        value={this.state.url}
                                        onChange={e =>
                                            this.setState({
                                                url: e.target.value
                                            })
                                        }
                                    />

                                    <TextField
                                        fullWidth
                                        id="filled-email-input"
                                        label="Link Title"
                                        className="mb-3"
                                        type="text"
                                        autoComplete="off"
                                        variant="standard"
                                        value={this.state.title}
                                        onChange={e =>
                                            this.setState({
                                                title: e.target.value
                                            })
                                        }
                                    />
                                </div>
                            </div>
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            autoFocus
                            onClick={() => this.setState({ model: false })}
                            color="primary"
                        >
                            cancel
                        </Button>
                        <Button
                            onClick={() => this.HandelSubmit()}
                            color="primary"
                            autoFocus
                        >
                            Save Change
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}

export default connect(null, { editLink })(LinkEditModel);
