import Axios from "axios";
import { MANAGE_CAMPAIGN, CAMPAIGN, CAM_UPDATE_STATUS, GET_PACKAGE } from "./type";
import { API, BillingURL } from "../../Global";
import { decode } from "../../Helpers";
export const getCampaign = () => dispatch => {
    Axios.post(API + "/campaign", { token: localStorage.getItem("auth-Token") })
        .then(res => {
            dispatch({
                type: CAMPAIGN,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const updateCamp = data => dispatch => {
    Axios.post(API + "/campaign", data)
        .then(res => {
            dispatch({
                type: MANAGE_CAMPAIGN,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};

export const getPackage = data => dispatch => {
    Axios.post(BillingURL + "/getpackage", data)
        .then(res => {
            dispatch({
                type: GET_PACKAGE,
                payload: res.data
            });
        })
        .catch(err => { });
};

export const camupdateStatus = () => dispatch => {
    dispatch({
        type: CAM_UPDATE_STATUS
    });
};
