import { SIDEBAR, INFO } from "./type";
import Axios from "axios";
import { API } from "../../Global"


export const sidebar = () => dispatch => {
    dispatch({
        type: SIDEBAR
    });
};

export const getinfo = () => dispatch => {
    Axios.post(API + "/info", { token: localStorage.getItem("auth-Token") })
        .then(res => {
            dispatch({
                type: INFO,
                payload: res.data
            });

        })
        .catch(err => { });
};
