import { CAMPAIGN, MANAGE_CAMPAIGN, CAM_UPDATE_STATUS, GET_PACKAGE } from "../actions/type";
const initialState = {
    campaign: null,
    update: false,
    package: null
};
// eslint-disable-next-line
export default function (state = initialState, actions) {
    switch (actions.type) {
        case CAMPAIGN:
            return {
                ...state,
                campaign: actions.payload
            };
        case MANAGE_CAMPAIGN:
            return {
                ...state,
                campaign: actions.payload,
                update: true
            };
        case CAM_UPDATE_STATUS:
            return {
                ...state,
                update: false
            };
        case GET_PACKAGE:
            return {
                ...state,
                package: actions.payload
            };
        default:
            return state;
    }
}
