import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";
import DialogContentText from "@material-ui/core/DialogContentText";
import TextField from "@material-ui/core/TextField";
import * as moment from "moment"
import { connect } from "react-redux";
import {
    getPop,
    updatePop,
    updatepopstatus,
    editpop
} from "../../redux/actions/pop";
import Switch from "@material-ui/core/Switch";


import {
    Card,
    Typography,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Snackbar
} from "@material-ui/core";
import Skeleton from "@material-ui/lab/Skeleton";
import MuiAlert from "@material-ui/lab/Alert";
import { Link } from "react-router-dom";

var expdate = moment(localStorage.getItem('expDate'))
var now = moment()
var day = expdate.diff(now, 'days')

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}
class PopMail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            model: false,
            popmail: null,
            _id: "",
            username: "",
            password: "",
            hostname: "",
            port: "",
            flag: null,
            update: false,
            success: false
        };
        this.handleChange = this.handleChange.bind(this);
        this.handelEdit = this.handelEdit.bind(this);
    }
    componentDidMount() {
        this.props.getPop();
    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            update: nextProps.PopMail.update,
            success: nextProps.PopMail.success
        });
        if (nextProps.PopMail !== null) {
            this.setState({
                popmail: nextProps.PopMail.PopMail,
                username: nextProps.PopMail.PopMail.username,
                password: nextProps.PopMail.PopMail.password,
                hostname: nextProps.PopMail.PopMail.hostname,
                port: nextProps.PopMail.PopMail.port,
                flag: nextProps.PopMail.PopMail.flag,
                id: nextProps.PopMail.PopMail._id
            });
        }
    }
    handelEdit() {
        const newData = {
            token: localStorage.getItem('auth-Token'),
            id: this.state.id,
            username: this.state.username,
            password: this.state.password,
            hostname: this.state.hostname,
            port: this.state.port,
            editpop: "editpop"
        };
        this.props.editpop(newData);
    }
    handleChange(id) {
        this.setState({
            flag: !this.state.flag
        });
        setTimeout(() => this.dataUpdate(id), 2000);
    }
    dataUpdate(id) {
        const newData = {
            token: localStorage.getItem('auth-Token'),
            status: this.state.flag,
            id: id
        };
        this.props.updatePop(newData);
    }
    render() {

        return (
            <div >
                <Snackbar
                    open={this.state.success}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() => {
                        this.setState({ success: false });
                        this.props.updatepopstatus();
                    }}
                >
                    <Alert
                        onClose={() => {
                            this.setState({ success: false });
                            this.props.updatepopstatus();
                        }}
                        severity="success"
                    >
                        POP3 Updates Success !!
                    </Alert>
                </Snackbar>
                <Header />
                <div className="wrapper">
                    <SideBar />
                    <div className="content-area overflow-hidden" style={{ minHeight: "83vh" }}>
                        <div className="page-header">
                            <h4 className="page-title">POP3</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="/console">
                                        Dashboard
                                    </Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    POP3
                                </li>
                            </ol>
                        </div>
                        <div className="row">
                            <div className="col-lg-12">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="body1"
                                        className="px-2 h-12 py-2 bg-gradient-success font-bold text-white justify-center"
                                    >
                                        POP3 Mail Setup
                                    </Typography>
                                    <div className="table-responsive">
                                        <table className="table ">
                                            <thead>
                                                <tr>
                                                    <th>Email</th>
                                                    <th>Password</th>
                                                    <th>Hostnane</th>
                                                    <th>Port</th>
                                                    <th>Status</th>
                                                    <th>Edit</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {this.state.popmail !== null ? (
                                                    <tr>
                                                        <td>{this.state.popmail.username}</td>
                                                        <td>{this.state.popmail.password}</td>
                                                        <td> {this.state.popmail.hostname}</td>
                                                        <td> {this.state.popmail.port}</td>

                                                        <td>
                                                            <Switch
                                                                size="small"
                                                                disabled={day <= 0 ? true : false}
                                                                checked={this.state.flag !== null && this.state.flag}
                                                                onChange={() => this.handleChange(this.state.popmail._id)}
                                                                inputProps={{ "aria-label": "secondary checkbox" }} />
                                                        </td>

                                                        <td>
                                                            <div className=" icon mr-2 cursor-pointer text-xl">
                                                                <i onClick={() => this.setState({ model: !this.state.model })} className="fas fa-edit "></i>
                                                            </div>

                                                        </td>
                                                    </tr>
                                                ) : (
                                                        <tr>
                                                            <td colSpan={7}>
                                                                <Skeleton variant="text" height={40} />
                                                            </td>
                                                        </tr>
                                                    )}
                                            </tbody>
                                        </table>
                                    </div>
                                </Card>
                            </div>
                        </div>
                    </div>

                    <Dialog
                        open={this.state.model}
                        onClose={() =>
                            this.setState({ model: !this.state.model })
                        }
                        aria-labelledby="responsive-dialog-title"
                    >
                        <DialogTitle id="responsive-dialog-title">
                            {"Do you Want to change POP Email ?"}
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText>
                                <form
                                    style={{
                                        margin: 2,
                                        width: "100%"
                                    }}
                                    className="flex flex-col"
                                    noValidate
                                    autoComplete="off"
                                >
                                    <TextField
                                        id="filled-email-input"
                                        label="Email"
                                        className="mb-3"
                                        type="text"
                                        autoComplete="off"
                                        variant="standard"
                                        value={this.state.username}
                                        onChange={e =>
                                            this.setState({
                                                username: e.target.value
                                            })
                                        }
                                    />
                                    <TextField
                                        id="filled-password-input"
                                        className="mb-3"
                                        label="Password"
                                        type="text"
                                        autoComplete="off"
                                        variant="standard"
                                        value={this.state.password}
                                        onChange={e =>
                                            this.setState({
                                                password: e.target.value
                                            })
                                        }
                                    />
                                    <TextField
                                        id="filled-hostname-input"
                                        className="mb-3"
                                        label="Hostname"
                                        type="text"
                                        autoComplete="off"
                                        variant="standard"
                                        value={this.state.hostname}
                                        onChange={e =>
                                            this.setState({
                                                hostname: e.target.value
                                            })
                                        }
                                    />
                                    <TextField
                                        id="filled-Post-input"
                                        className="mb-3"
                                        label="Port"
                                        type="text"
                                        autoComplete="off"
                                        variant="standard"
                                        value={this.state.port}
                                        onChange={e =>
                                            this.setState({
                                                port: e.target.value
                                            })
                                        }
                                    />
                                </form>
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button
                                onClick={() =>
                                    this.setState({ model: !this.state.model })
                                }
                                color="secondary"
                            >
                                Cancel
                            </Button>
                            <Button
                                onClick={() => {
                                    this.setState({ model: !this.state.model });
                                    this.handelEdit();
                                }}
                                color="primary"

                            >
                                Submit
                            </Button>
                        </DialogActions>
                    </Dialog>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    PopMail: state.PopMail
});
export default connect(mapStateToProps, {
    getPop,
    updatePop,
    updatepopstatus,
    editpop
})(PopMail);
