import React, { Component } from "react";
import {
    DialogTitle,
    DialogContent,
    DialogContentText,
    TextField,
    Button,
    Dialog,
    DialogActions,
  
} from "@material-ui/core";
import { connect } from "react-redux";
import { editTrigger } from "../../../redux/actions/Trigger";
class TriggerEditModel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            model: false,
            trigger: "",
            alttext: "",
            id: ""
        };
    }
    componentDidMount() {
        this.setState({
            id: this.props.data._id,
            trigger: this.props.data.trigger,
            alttext: this.props.data.alttext
        });
    }
    HandelSubmit() {
        const newData = {
            token: localStorage.getItem("auth-Token"),
            trigger: this.state.trigger,
            alttext: this.state.alttext,
            id: this.state.id
        };
        this.props.editTrigger(newData);
        this.setState({ model: false });
    }

    render() {
        return (
            <div>
                <div
                    onClick={() => {
                        this.setState({ model: true });
                    }}
                    className="icon mr-2 cursor-pointer text-xl"
                >
                    <i className="fas fa-edit "></i>
                </div>
                <Dialog
                    fullWidth={true}
                    maxWidth="xs"
                    open={this.state.model}
                    onClose={() => this.setState({ model: false })}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        {"Do you Want to change Triggar ?"}
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            <div class="row ml-2">
                                <div className="col-sm-12">
                                    <TextField
                                        fullWidth
                                        id="filled-email-input"
                                        label="Trigger"
                                        className="mb-3"
                                        type="text"
                                        autoComplete="off"
                                        variant="standard"
                                        value={this.state.trigger}
                                        onChange={e =>
                                            this.setState({
                                                trigger: e.target.value
                                            })
                                        }
                                    />

                                    <TextField
                                        fullWidth
                                        id="filled-email-input"
                                        label="Alternative Text"
                                        className="mb-3"
                                        type="text"
                                        autoComplete="off"
                                        variant="standard"
                                        value={this.state.alttext}
                                        onChange={e =>
                                            this.setState({
                                                alttext: e.target.value
                                            })
                                        }
                                    />
                                </div>
                            </div>
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            autoFocus
                            onClick={() => this.setState({ model: false })}
                            color="primary"
                        >
                            cancel
                        </Button>
                        <Button
                            onClick={() => this.HandelSubmit()}
                            color="primary"
                            autoFocus
                        >
                            Save Change
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}

export default connect(null, { editTrigger })(TriggerEditModel);
