import Axios from "axios";
import { TRIGGER, ADD_TRIGGER, EDIT_TRIGGER, DELETE_TRIGGER, TRIGGER_STATUS } from "./type";
import { API } from "../../Global";
import { decode } from "../../Helpers";
export const getTrigger = () => dispatch => {
    Axios.post(API + "/getTrigger", { token: localStorage.getItem("auth-Token") })
        .then(res => {

            dispatch({
                type: TRIGGER,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const addTrigger = data => dispatch => {
    Axios.post(API + "/addTrigger", data)
        .then(res => {

            dispatch({
                type: ADD_TRIGGER,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const editTrigger = data => dispatch => {
    Axios.post(API + "/editTrigger", data)
        .then(res => {
            dispatch({
                type: EDIT_TRIGGER,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const deleteTrigger = data => dispatch => {
    Axios.post(API + "/deleteTrigger", data)
        .then(res => {
            dispatch({
                type: DELETE_TRIGGER,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const updateTriggerStatus = () => dispatch => {
    dispatch({
        type: TRIGGER_STATUS
    });
};
