import CryptoJS from "crypto-js"
import {
    Base64
} from 'js-base64';
import {
    myKey
} from "./Global"
export function decode(data) {
    const encrypted_json = JSON.parse(Base64.decode(data));
    const decrypted = CryptoJS.AES.decrypt(encrypted_json.value, CryptoJS.enc.Base64.parse(myKey), {
        iv: CryptoJS.enc.Base64.parse(encrypted_json.iv)
    });
    const  returndata = decrypted.toString(CryptoJS.enc.Utf8)
    return JSON.parse(returndata);
}
export function substr_count(string, substring, start, length) {
    var c = 0;
    if (start) {
        string = string.substr(start);
    }
    if (length) {
        string = string.substr(0, length);
    }
    for (var i = 0; i < string.length; i++) {
        if (substring === string.substr(i, substring.length))
            c++;
    }
    return c;
}

// eslint-disable-next-line
export function replaceAll(string, find, replace) {
    for (var i = 0; i < find.length; i++) {
        // eslint-disable-next-line
        var string = string.replace(new RegExp(find[i], 'g'), replace[i]);
    }
    return string;
}
