import React, { Component } from "react";
import {
    ListItem,
    ListItemIcon,
    List,
    ListItemText,
    Button,
    Card,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    TextField,
    Snackbar,
    Divider,
    Tooltip
} from "@material-ui/core";
import DashboardIcon from "@material-ui/icons/Dashboard";
import SettingsIcon from "@material-ui/icons/Settings";
import MailIcon from "@material-ui/icons/Mail";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import MessageIcon from "@material-ui/icons/Message";
import { Link, withRouter } from "react-router-dom";
import LinkIcon from "@material-ui/icons/Link";
import PhotoLibraryIcon from "@material-ui/icons/PhotoLibrary";
import { connect } from "react-redux";
import { sidebar, getinfo } from "../../redux/actions/header";
import Clock from "react-live-clock";
import * as moment from "moment";
import MuiAlert from "@material-ui/lab/Alert";
import Axios from "axios";
import { API } from "../../Global";
import DeleteIcon from "@material-ui/icons/Delete";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import CancelIcon from "@material-ui/icons/Cancel";
import FindReplaceIcon from "@material-ui/icons/FindReplace";
import LiveChat from "react-livechat";
import QuestionAnswerIcon from "@material-ui/icons/QuestionAnswer";
import UserIMg from "../assets/img/user.png"
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}
class SideBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            sidebar: false,
            replyopen: false,
            fopen: false,
            info: null,
            name: null,
            popLimit: 0,
            expDate: null,
            usedlimit: 0,
            model: false,
            email: null,
            success: false,
            message: "",
            UserPOP: true,
            SmtpStatus: true,
            chat: 2,
            domain: null,
            chatModel: false,
            c_id: ""
        };
        this.days = this.days.bind(this);
        this.handelDeleteLeads = this.handelDeleteLeads.bind(this);
    }

    componentDidMount() {
        this.props.sidebar();
        this.props.getinfo();
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.side.Info !== null) {
            this.setState({
                info: {
                    name: nextProps.side.Info.name,
                    email: nextProps.side.Info.email
                },
                name: nextProps.side.Info.name,
                email: nextProps.side.Info.email,
                popLimit: nextProps.side.Info.popLimit,
                expDate: nextProps.side.Info.expDate,
                usedlimit: nextProps.side.Info.usedlimit,
                UserPOP: nextProps.side.Info.UserPOP,
                SmtpStatus: nextProps.side.Info.SmtpStatus,
                chat: nextProps.side.Info.chatEnable,
                domain: nextProps.side.Info.domain,
                c_id: nextProps.side.Info.c_id
            });
        }
    }
    handelDeleteLeads() {
        Axios.post(API + "/deleteTest", {
            token: localStorage.getItem("auth-Token"),
            email: this.state.email
        })
            .then(res => {
                if (res.data.success) {
                    this.setState({
                        success: true,
                        message: this.state.email + " Deleted Success !!"
                    });
                } else {
                    this.setState({
                        success: true,
                        message: "No Email Found In Database"
                    });
                }
            })
            .catch(err => {});
    }
    days() {
        if (this.state.expDate !== null) {
            var expdate = moment(this.state.expDate).endOf("day");
            var now = moment();
            var day = expdate.diff(now, "days");
            if (day <= 1) {
                return (
                    <Card className="px-1 py-1 ml-2 mr-2 bg-gradient-danger">
                        <Link to="/Payment">Paymnet Required</Link>
                    </Card>
                );
            }
            if (day <= 7) {
                return (
                    <Card className="px-1 py-1 ml-2 mr-2 bg-gradient-danger">
                        <Link to="/Payment">You Have {day} Days Left</Link>
                    </Card>
                );
            } else {
                return (
                    <Card className="px-1 py-1 ml-2 mr-2 bg-gradient-success">
                        <div>You Have {day} Days Left</div>
                    </Card>
                );
            }
        }
    }
    render() {
        // let host = window.location.host;
        // let parts = host.split(".");
        // let subdomain = "";

        // if (parts.length >= 3) {
        //     subdomain = parts[0];
        // }

        return (
            <nav
                id="sidebar"
                className={
                    this.props.side.side === false
                        ? "nav-sidebar active sm:min-h-full "
                        : "nav-sidebar sm:min-h-full"
                }
            >
                {this.state.info !== null && (
                    <LiveChat
                        onChatLoaded={ref => (this.livechat = ref)}
                        license={11989521}
                        visitor={{
                            name: this.state.info.name,
                            email: this.state.info.email
                        }}
                    />
                )}

                <Snackbar
                    open={this.state.success}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() =>
                        this.setState({ success: false, message: "" })
                    }
                >
                    <Alert
                        onClose={() =>
                            this.setState({
                                success: false,
                                message: ""
                            })
                        }
                        severity="info"
                    >
                        {this.state.message}
                    </Alert>
                </Snackbar>
                <Dialog
                    fullWidth
                    open={this.state.model}
                    onClose={() => this.setState({ model: false })}
                    aria-labelledby="form-dialog-title"
                >
                    <DialogTitle id="form-dialog-title">
                        {" "}
                        Delete Test Email
                    </DialogTitle>
                    <DialogContent>
                        <div className="col-lg-12 mb-4">
                            <TextField
                                margin="dense"
                                id="name"
                                label="Your test email"
                                type="email"
                                fullWidth
                                onChange={e =>
                                    this.setState({
                                        email: e.target.value
                                    })
                                }
                            />
                        </div>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            onClick={() => this.setState({ model: false })}
                            color="primary"
                        >
                            Cancel
                        </Button>
                        <Button
                            onClick={() => {
                                this.setState({ model: false });
                                this.handelDeleteLeads();
                            }}
                            color="primary"
                        >
                            Delete
                        </Button>
                    </DialogActions>
                </Dialog>
                <ul className="list-unstyled components" id="accordion">
                    <div className="user-profile">
                        <div className="dropdown user-pro-body">
                            <Link to="/Account">
                                <img
                                    src={UserIMg}
                                    alt="user-img"
                                />
                            </Link>

                            <div className="u-dropdown" role="button">
                                <span className="font-weight-semibold">
                                    <Clock
                                        format={"hh:mm:ss A"}
                                        ticking={true}
                                        timezone={"Asia/Dhaka"}
                                    />
                                    <br />
                                    Client Name: {this.state.name}
                                    <br />
                                    POP LIMIT : {this.state.popLimit}
                                    <br />
                                    Available Limit :{" "}
                                    {this.state.popLimit - this.state.usedlimit}
                                    <br />
                                    {this.days()}
                                </span>
                            </div>
                            <br />
                        </div>
                    </div>
                    <li>
                        <div className="flex flex-col bg-white text-gray-600">
                            <List
                                component="nav"
                                aria-labelledby="nested-list-subheader"
                            >
                                <Link
                                    to="console"
                                    className="text-decoration-none p-0 sideXmenu"
                                >
                                    <ListItem button>
                                        <ListItemIcon>
                                            <DashboardIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="Dashboard" />
                                    </ListItem>
                                </Link>

                                <Link
                                    to="ManageCampaign"
                                    className="text-decoration-none p-0 sideXmenu"
                                >
                                    <ListItem button>
                                        <ListItemIcon>
                                            <SettingsIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="Manage Campaign" />
                                    </ListItem>
                                </Link>

                                <Link
                                    to="POP3"
                                    className="text-decoration-none p-0 sideXmenu"
                                >
                                    <ListItem button>
                                        <ListItemIcon>
                                            <MailIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="POP Mail" />
                                    </ListItem>
                                </Link>

                                <Link
                                    to="Image"
                                    className="text-decoration-none p-0 sideXmenu"
                                >
                                    <ListItem button>
                                        <ListItemIcon>
                                            <PhotoLibraryIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="Image Manager" />
                                    </ListItem>
                                </Link>

                                <Link
                                    to="Link"
                                    className="text-decoration-none p-0 sideXmenu"
                                >
                                    <ListItem button>
                                        <ListItemIcon>
                                            <LinkIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="Link Manager" />
                                    </ListItem>
                                </Link>

                                <Link
                                    to="Trigger"
                                    className="text-decoration-none p-0 sideXmenu"
                                >
                                    <ListItem button className="pl-4">
                                        <ListItemIcon>
                                            <FindReplaceIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="Trigger Manager" />
                                    </ListItem>
                                </Link>

                                <Link
                                    to="AllReply"
                                    className="text-decoration-none p-0 sideXmenu"
                                >
                                    <ListItem button className="pl-4">
                                        <ListItemIcon>
                                            <MessageIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="All Reply" />
                                    </ListItem>
                                </Link>

                                <Link
                                    to="AllFollowup"
                                    className="text-decoration-none p-0 sideXmenu"
                                >
                                    <ListItem button>
                                        <ListItemIcon>
                                            <InboxIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="All Followup" />
                                    </ListItem>
                                </Link>

                                <List
                                    onClick={() =>
                                        this.setState({ model: true })
                                    }
                                    component="div"
                                    disablePadding
                                >
                                    <ListItem button className="pl-4">
                                        <ListItemIcon>
                                            <DeleteIcon />
                                        </ListItemIcon>

                                        <ListItemText primary="Delete Email" />
                                    </ListItem>
                                </List>

                                {this.state.chat === 1 && (
                                    <>
                                        <Link
                                            to="Chat"
                                            className="text-decoration-none p-0 sideXmenu"
                                        >
                                            <ListItem button>
                                                <ListItemIcon>
                                                    <QuestionAnswerIcon />
                                                </ListItemIcon>

                                                <ListItemText primary="Chat Reports" />
                                            </ListItem>
                                        </Link>

                                        <Link
                                            to="ChatLinks"
                                            className="text-decoration-none p-0 sideXmenu"
                                        >
                                            <ListItem button>
                                                <ListItemIcon>
                                                    <QuestionAnswerIcon />
                                                </ListItemIcon>

                                                <ListItemText primary="Chat Links" />
                                            </ListItem>
                                        </Link>
                                    </>
                                )}

                                <Divider />
                                <div className="px-2 py-1   justify-center ">
                                    <div className="h-10 text-center font-bold text-black">
                                        Server Status
                                    </div>
                                    <div className="h-10 flex flex-row justify-center">
                                        <div className="mr-1">
                                            POP :{" "}
                                            {this.state.UserPOP === true ? (
                                                <Tooltip title="OK">
                                                    <CheckCircleIcon className="text-green-800 font-semibold" />
                                                </Tooltip>
                                            ) : (
                                                <Tooltip title="Inactive">
                                                    <CancelIcon className="text-red-800 font-semibold" />
                                                </Tooltip>
                                            )}
                                        </div>
                                        <div>
                                            SMTP :
                                            {this.state.SmtpStatus === true ? (
                                                <Tooltip title="OK">
                                                    <CheckCircleIcon className="text-green-800 font-semibold" />
                                                </Tooltip>
                                            ) : (
                                                <Tooltip title="Inactive">
                                                    <CancelIcon className="text-red-800 font-semibold" />
                                                </Tooltip>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </List>
                        </div>
                    </li>
                </ul>
            </nav>
        );
    }
}

const mapStateToProps = state => ({
    side: state.side
});

export default withRouter(
    connect(
        mapStateToProps,
        { sidebar, getinfo }
    )(SideBar)
);
