import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";
import { connect } from "react-redux";
import {
    getToalstats,
    getTodaystats,
    getletestLeads,
    replyStatus
} from "../../redux/actions/Dashboard";
import TodayTable from "./homeComponent/todayTable";
import TotalTable from "./homeComponent/totalTable";
import LetestLeadsTable from "./homeComponent/letestLeads";
import ReplyStatusTable from "./homeComponent/replystatus";
import { Link } from "react-router-dom";

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            totalStatus: null,
            todayStatus: null,
            letestLeads: null,
            ReplyStatus: null,

        };
        this.reload = this.reload.bind(this);
    }
    reload(table) {
        if (table === "todaytable") {
            this.setState({
                todayStatus: null
            });
            this.props.getTodaystats();
        }

        if (table === "totaltable") {
            this.setState({
                totalStatus: null
            });
            this.props.getToalstats();
        }

        if (table === "letestLeads") {
            this.setState({
                letestLeads: null
            });
            this.props.getletestLeads();
        }
        if (table === "replystatus") {
            this.setState({
                ReplyStatus: null
            });
            this.props.replyStatus();
        }
    }
    componentDidMount() {
        this.props.getToalstats();
        this.props.getTodaystats();
        this.props.getletestLeads();
        this.props.replyStatus();
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.TotalLeads.TotalLeads !== null) {
            this.setState({
                totalStatus: nextProps.TotalLeads.TotalLeads
            });
        }
        if (nextProps.TotalLeads.TodayLeads !== null) {
            this.setState({
                todayStatus: nextProps.TotalLeads.TodayLeads
            });
        }
        if (nextProps.TotalLeads.LetestLeads !== null) {
            this.setState({
                letestLeads: nextProps.TotalLeads.LetestLeads
            });
        }
        if (nextProps.TotalLeads.ReplyStatus !== null) {
            this.setState({
                ReplyStatus: nextProps.TotalLeads.ReplyStatus
            });
        }
    }




    render() {
        return (
            <div>

                <Header />
                <div className="wrapper">
                    <SideBar />
                    <div className=" content-area overflow-hidden" style={{ minHeight: "83vh" }}>
                        <div className="page-header">
                            <h4 className="page-title">Dashboard</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="console">
                                        Dashboard
                                    </Link>
                                </li>

                            </ol>
                        </div>
                        <div className="row">
                            <div className="col-lg-6 mb-4">
                                <TodayTable
                                    action={() => this.reload("todaytable")}
                                    data={
                                        this.state.todayStatus !== null &&
                                        this.state.todayStatus
                                    }
                                />
                            </div>
                            <div className="col-lg-6 mb-4">
                                <TotalTable
                                    action={() => this.reload("totaltable")}
                                    data={
                                        this.state.totalStatus !== null &&
                                        this.state.totalStatus
                                    }
                                />
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-lg-6 mb-4">
                                <LetestLeadsTable
                                    action={() => this.reload("letestLeads")}
                                    data={
                                        this.state.letestLeads !== null &&
                                        this.state.letestLeads
                                    }
                                />
                            </div>

                            <div className="col-lg-6">
                                <ReplyStatusTable
                                    action={() => this.reload("replystatus")}
                                    data={
                                        this.state.ReplyStatus !== null &&
                                        this.state.ReplyStatus
                                    }
                                />
                            </div>
                        </div>
                    </div>
                </div >
            </div >
        );
    }
}
const mapStateToProps = state => ({
    TotalLeads: state.TotalLeads
});
export default connect(mapStateToProps, {
    getToalstats,
    getTodaystats,
    getletestLeads,
    replyStatus
})(Home);
