import {
    CHAT_LINK,
    CHAT_ADD_LINK,
    CHAT_EDIT_LINK,
    CHAT_DELETE_LINK,
    CHAT_LINK_STATUS
} from "../actions/type";
const initialState = {
    Link: null,
    success: false,
    message: ""
};
// eslint-disable-next-line
export default function(state = initialState, actions) {
    switch (actions.type) {
        case CHAT_LINK:
            return {
                ...state,
                Link: actions.payload
            };
        case CHAT_ADD_LINK:
            return {
                ...state,
                Link: actions.payload,
                success: true,
                message: "Link Added"
            };
        case CHAT_LINK_STATUS:
            return {
                ...state,
                success: false,
                message: ""
            };
        case CHAT_EDIT_LINK:
            return {
                ...state,
                Link: actions.payload,
                success: true,
                message: "Link Updated"
            };
        case CHAT_DELETE_LINK:
            return {
                ...state,
                Link: actions.payload,
                success: true,
                message: "Link Deleted"
            };
        default:
            return state;
    }
}
