import Axios from "axios";
import { POP_MAIL, UPDATE_POP, UPDATE_POP_STATUS, EDIT_POP } from "./type";
import { API } from "../../Global";

import { decode } from "../../Helpers"
export const getPop = () => dispatch => {
    Axios.post(API + "/popmail", { token: localStorage.getItem("auth-Token") })
        .then(res => {
            dispatch({
                type: POP_MAIL,
                payload: decode(res.data)
            });

        })
        .catch(err => { });
};
export const updatePop = data => dispatch => {
    Axios.post(API + "/updatepop", data)
        .then(res => {
            dispatch({
                type: UPDATE_POP,
                payload: decode(res.data)
            });


        })
        .catch(err => { });
};

export const editpop = data => dispatch => {
    Axios.post(API + "/updatepop", data)
        .then(res => {
            dispatch({
                type: EDIT_POP,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const updatepopstatus = () => dispatch => {
    dispatch({
        type: UPDATE_POP_STATUS
    });
};

