import {
    LOGIN_ACTION,
    SET_LOGIN_ERROR,
    GET_USER_DATA,
    LOGOUT,
    
} from "../actions/type";
const initialState = {
    isAuth: false,
    error: {},
    userData: null
};
// eslint-disable-next-line
export default function(state = initialState, actions) {
    switch (actions.type) {
        case LOGIN_ACTION:
            return {
                ...state,
                isAuth: true
            };

        case SET_LOGIN_ERROR:
            return {
                ...state,
                error: actions.payload
            };
        case GET_USER_DATA:
            return {
                ...state,
                userData: actions.payload
            };
        case LOGOUT:
            return {
                ...state,
                isAuth: false
            };

        default:
            return state;
    }
}
