import { SIDEBAR, INFO } from "../actions/type";
import { isMobile } from 'react-device-detect';
const initialState = {
    side: isMobile ? true : true,
    Info: null
};
// eslint-disable-next-line
export default function (state = initialState, actions) {
    switch (actions.type) {
        case SIDEBAR:
            return {
                ...state,
                side: !state.side
            };
        case INFO:
            return {
                ...state,
                Info: actions.payload
            };
        default:
            return state;
    }
}
