import React, { Component } from "react";
import { AppBar } from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import IconButton from "@material-ui/core/IconButton";
import Badge from "@material-ui/core/Badge";
import { connect } from "react-redux";
import { sidebar } from "../../redux/actions/header";
import CloseIcon from "@material-ui/icons/Close";
import Logo from "../assets/img/mainlogo.svg";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import { withRouter } from "react-router-dom";
import { LogOUT } from "../../redux/actions/Login";
import DevicesIcon from "@material-ui/icons/Devices";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { QRCode } from "react-qrcode-logo";
import Qrlogo from "../assets/img/qrlogo.svg";

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            anchorEl: null,
            isMenuOpen: false,
            model: false,
        };
        this.handelLogout = this.handelLogout.bind(this);
    }
    componentDidMount() {
        this.props.sidebar();

    }

    handelLogout() {
        localStorage.removeItem("auth-Token");
        localStorage.removeItem("expDate");

        this.props.LogOUT();
        this.props.history.push("/");
    }
    render() {
        return (
            <div>
                <AppBar
                    elevation={0}
                    position="static"
                    style={{
                        borderBottom: "1px solid rgba(9, 9, 9, 0.14)",
                    }}
                    className="app-header1 bg-white py-1 d-flex flex-row"
                >
                    <div className="container-fluid">
                        <div className="d-flex">
                            <IconButton
                                id="sidebarCollapse"
                                edge="start"
                                aria-label="menu"
                                onClick={() => this.props.sidebar()}
                            >
                                {this.props.side.side === true ? (
                                    <CloseIcon />
                                ) : (
                                        <MenuIcon />
                                    )}
                            </IconButton>

                            <div className="header-brand">
                                <img
                                    src={Logo}
                                    className="header-brand-img"
                                    style={{
                                        height: "40px ",
                                        width: "181px",
                                    }}
                                    alt="logo"
                                />
                            </div>
                        </div>
                    </div>
                    <div>
                        <Dialog
                            open={this.state.model}
                            onClose={() => this.setState({ model: false })}
                            aria-labelledby="alert-dialog-title"
                            aria-describedby="alert-dialog-description"
                        >
                            <DialogTitle id="alert-dialog-title">
                                {"To connet our mobile app"}
                            </DialogTitle>
                            <DialogContent className="items-center">
                                <DialogContentText id="alert-dialog-description">
                                    <div className="lg:flex flex-row justify-center ">
                                        <div className="text-lg ">
                                            1. Open Our App on your Phone <br /><br />
                                            2. Tap Login URL <br /><br />
                                            3. Point your phone to this screento
                                            capture the code
                                        </div>
                                        <div className="ml-2">
                                            <QRCode
                                                logoImage={Qrlogo}
                                                logoWidth={50}
                                                size={220}
                                                value={window.link}
                                            />
                                        </div>
                                    </div>
                                </DialogContentText>
                            </DialogContent>
                            <DialogActions>
                                <Button
                                    onClick={() =>
                                        this.setState({ model: false })
                                    }
                                    color="primary"
                                >
                                    close
                                </Button>
                            </DialogActions>
                        </Dialog>
                    </div>
                    <div className="flex ml-10 mr-10">
                        <IconButton
                            onClick={() => this.setState({ model: true })}
                            aria-label="show 17 new notifications"
                            color="primary"
                        >
                            <Badge badgeContent={0} color="secondary">
                                <DevicesIcon />
                            </Badge>
                        </IconButton>
                        <IconButton
                            edge="end"
                            aria-label="log Out"
                            aria-haspopup="true"
                            color="secondary"
                            onClick={() => this.handelLogout()}
                        >
                            <ExitToAppIcon className="font-bold" />
                        </IconButton>
                    </div>
                </AppBar>
            </div>
        );
    }
}
const mapStateToProps = (state) => ({
    side: state.side,
});
export default withRouter(
    connect(mapStateToProps, { sidebar, LogOUT })(Header)
);
