import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";
import { Card, TextField, Button } from "@material-ui/core";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import CKEditor from "ckeditor4-react";
import { connect } from "react-redux";
import { createFollowup, followupStatus } from "../../redux/actions/followup";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import { Link } from "react-router-dom";
import SendIcon from "@material-ui/icons/Send";
import BlockIcon from "@material-ui/icons/Block";
import Token from "../common/token";

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

class CreateFollowup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            replynum: "",
            messagebody: "",
            interval: 0,
            replyAllow: 10,
        };
        this.handelSubmit = this.handelSubmit.bind(this);
    }
    componentDidMount() {
        this.props.followupStatus();
    }
    handelSubmit() {
        const newData = {
            token: localStorage.getItem("auth-Token"),
            replynum: this.state.replynum,
            interval: this.state.interval,
            messagebody: this.state.messagebody,
        };
        this.props.createFollowup(newData);
        setTimeout(() => this.props.history.push("/AllFollowup"), 1000);
    }

    render() {
        let array = [];
        const allow = () => {
            for (let index = 1; index <= this.state.replyAllow; index++) {
                array.push(
                    <MenuItem key={index} value={index}>
                        <em>{index}</em>
                    </MenuItem>
                );
            }
            return array;
        };

        // console.log(array);
        return (
            <div>
                <Snackbar
                    open={this.props.Followup.success}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center",
                    }}
                    onClose={() => this.props.followupStatus()}
                >
                    <Alert
                        onClose={() => this.props.followupStatus()}
                        severity="success"
                    >
                        {this.props.Followup.message}
                    </Alert>
                </Snackbar>
                <Header />
                <div className="wrapper">
                    <SideBar />
                    <div
                        className="content-area overflow-hidden"
                        style={{ minHeight: "83vh" }}
                    >
                        <div className="page-header">
                            <h4 className="page-title">Create Followup</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="console">Dashboard</Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    Create Followup
                                </li>
                            </ol>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)",
                                    }}
                                >
                                    <div className="flex flex-row justify-between bg-gradient-success items-center h-12">
                                        <div className="px-2 py-2  font-bold text-white">
                                            Create Followup Message
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <FormControl className="m1 mw-120">
                                            <InputLabel id="demo-simple-select-helper-label">
                                                Order by
                                            </InputLabel>
                                            <Select
                                                labelId="demo-simple-select-helper-label"
                                                id="demo-simple-select-helper"
                                                required
                                                value={this.state.replynum}
                                                onChange={(e) =>
                                                    this.setState({
                                                        replynum:
                                                            e.target.value,
                                                    })
                                                }
                                            >
                                                {allow()}
                                            </Select>
                                            <FormHelperText>
                                                Your Replay Number
                                            </FormHelperText>
                                        </FormControl>
                                    </div>

                                    <div className="col-sm-6">
                                        <div className="form-group row">
                                            <div className="col-sm-9">
                                                <TextField
                                                    id="filled-email-input"
                                                    label="Delay time (min)"
                                                    className="mb-3"
                                                    type="number"
                                                    required
                                                    autoComplete="off"
                                                    variant="standard"
                                                    onChange={(e) =>
                                                        this.setState({
                                                            interval:
                                                                e.target.value,
                                                        })
                                                    }
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="px-4 py-2">
                                        <Token />
                                    </div>
                                    <div className="col-sm-12">
                                        <div className="form-group">
                                            <label htmlFor="groupname">
                                                Content
                                            </label>
                                            <CKEditor
                                                onChange={(e) =>
                                                    this.setState({
                                                        messagebody: e.editor.getData(),
                                                    })
                                                }
                                                data={this.state.messagebody}
                                            />
                                        </div>

                                        <div className="card-footer ">
                                            <div className="flex flex-row justify-between float-right mb-2">
                                                <Link to={"/AllFollowup"}>
                                                    <Button
                                                        className="text-white justify-end  mr-3"
                                                        color="secondary"
                                                        variant="contained"
                                                    >
                                                        <BlockIcon className="mr-1" />{" "}
                                                        Cancel
                                                    </Button>
                                                </Link>
                                                <Button
                                                    variant="contained"
                                                    color="primary"
                                                    onClick={() =>
                                                        this.handelSubmit()
                                                    }
                                                >
                                                    {" "}
                                                    <SendIcon className="mr-1" />{" "}
                                                    submit
                                                </Button>
                                            </div>
                                        </div>
                                    </div>
                                </Card>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = (state) => ({
    Followup: state.Followup,
});
export default connect(mapStateToProps, { createFollowup, followupStatus })(
    CreateFollowup
);
