import {
    ALL_REPLY,
    ADD_REPLY,
    EDIT_REPLY,
    REPLY_UPDATE_STATS,
    DELETE_REPLY
} from "../actions/type";
const initialState = {
    Reply: null,
    success: false,
    message: ""
};
// eslint-disable-next-line
export default function (state = initialState, actions) {
    switch (actions.type) {
        case ALL_REPLY:
            return {
                ...state,
                Reply: actions.payload
            };
        case EDIT_REPLY:
            return {
                ...state,
                Reply: actions.payload.replydata,
                success: actions.payload.success,
                message: actions.payload.message
            };
        case DELETE_REPLY:
            return {
                ...state,
                Reply: actions.payload.replydata,
                success: actions.payload.success,
                message: actions.payload.message
            };
        case REPLY_UPDATE_STATS:
            return {
                ...state,
                success: false,
                message: ""
            };
        case ADD_REPLY:
            return {
                ...state,
                Reply: actions.payload.replydata,
                success: actions.payload.success,
                message: actions.payload.message
            };

        default:
            return state;
    }
}
