import Axios from "axios";
import {
    TOTAL_STATISTICS,
    TODAY_STATISTICS,
    LETEST_LEADS,
    REPLY_STATUS
} from "./type";
import {
    API
} from "../../Global";


export const getToalstats = () => dispatch => {
    Axios.post(API + "/totalleads", {
        token: localStorage.getItem("auth-Token")
    })
        .then(res => {
            dispatch({
                type: TOTAL_STATISTICS,
                payload: res.data
            });

        })
        .catch(err => { });
};

