import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";
import DialogContentText from "@material-ui/core/DialogContentText";
import TextField from "@material-ui/core/TextField";
import { connect } from "react-redux";
import {
    getCampaign,
    updateCamp,
    camupdateStatus
} from "../../redux/actions/campaign";
import MuiAlert from "@material-ui/lab/Alert";
import { Link } from "react-router-dom";
import {
    Card,
    Typography,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Snackbar
} from "@material-ui/core";
import Skeleton from "@material-ui/lab/Skeleton";
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}
class ManageCampaign extends Component {
    constructor(props) {
        super(props);
        this.state = {
            model: false,
            campaign: null,
            campaignName: "",
            success: false
        };
        this.handelUpdate = this.handelUpdate.bind(this);
    }
    componentDidMount() {
        this.props.getCampaign();
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.campaign !== null) {
            this.setState({
                campaign: nextProps.campaign.campaign,
                campaignName: nextProps.campaign.campaign.name
            });
        }
        if (nextProps.campaign.update !== false) {
            this.setState({
                success: nextProps.campaign.update
            });
        }
    }

    handelUpdate() {
        const newData = {
            token: localStorage.getItem("auth-Token"),
            name: this.state.campaignName,
            update: "updateData"
        };
        this.props.updateCamp(newData);
    }
    render() {
        return (
            <div>
                <Snackbar
                    open={this.state.success}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() => {
                        this.setState({ success: false });
                        this.props.camupdateStatus();
                    }}
                >
                    <Alert
                        onClose={() => {
                            this.setState({ success: false });
                            this.props.camupdateStatus();
                        }}
                        severity="success"
                    >
                        Model Name Update Success !!
                    </Alert>
                </Snackbar>
                <Header />
                <div className="wrapper">
                    <SideBar />
                    <div className="content-area overflow-hidden " style={{ minHeight: "83vh" }}>
                        <div className="page-header">
                            <h4 className="page-title">Manage Campaign</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="/console">
                                        Dashboard
                                    </Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    Manage Campaign
                                </li>
                            </ol>
                        </div>
                        <div className="row">
                            <div className="col-lg-12">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="body1"
                                        className="px-2 py-2 bg-gradient-success font-bold text-white"
                                    >
                                        Campaign
                                    </Typography>
                                    <table className="table table-responsive">
                                        <thead>
                                            <tr>
                                                <th>Campaign Name</th>
                                                <th>Daily Limit</th>
                                                <th>EXP Date</th>
                                                <th>Edit</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {this.state.campaign === null ? (
                                                <tr>
                                                    <td colSpan={4}>
                                                        <Skeleton
                                                            variant="text"
                                                            height={30}
                                                        />
                                                    </td>
                                                </tr>
                                            ) : (
                                                    <tr>
                                                        <td>
                                                            {
                                                                this.state.campaign
                                                                    .name
                                                            }
                                                        </td>

                                                        <td>
                                                            {
                                                                this.state.campaign
                                                                    .campaignlimit
                                                            }
                                                        </td>
                                                        <td>
                                                            {
                                                                this.state.campaign
                                                                    .campaignexp
                                                            }
                                                        </td>
                                                        <td>
                                                            <div className="icon mr-2 cursor-pointer text-xl">
                                                               
                                                                <i
                                                                    onClick={() =>
                                                                        this.setState(
                                                                            {
                                                                                model: !this
                                                                                    .state
                                                                                    .model
                                                                            }
                                                                        )
                                                                    }
                                                                    className="fas fa-edit "
                                                                ></i>
                                                            </div>
                                                        </td>
                                                    </tr>
                                                )}
                                        </tbody>
                                    </table>
                                </Card>
                            </div>
                        </div>
                    </div>

                    <Dialog
                        open={this.state.model}
                        onClose={() =>
                            this.setState({ model: !this.state.model })
                        }
                        aria-labelledby="responsive-dialog-title"
                    >
                        <DialogTitle id="responsive-dialog-title">
                            {"Do you Want to change Model Name ?"}
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText>
                                <TextField
                                    id="filled-password-input"
                                    label="Model Name"
                                    type="text"
                                    autoComplete="off"
                                    variant="standard"
                                    value={this.state.campaignName}
                                    onChange={e =>
                                        this.setState({
                                            campaignName: e.target.value
                                        })
                                    }
                                />
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button

                                onClick={() =>
                                    this.setState({ model: !this.state.model })
                                }
                                color='secondary'
                            >
                                Cancel
                            </Button>
                            <Button
                                onClick={() => {
                                    this.setState({ model: !this.state.model });
                                    this.handelUpdate();
                                }}
                                color="primary"
                            >
                                Submit
                            </Button>
                        </DialogActions>
                    </Dialog>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    campaign: state.campaign
});
export default connect(mapStateToProps, {
    getCampaign,
    updateCamp,
    camupdateStatus
})(ManageCampaign);
