import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";
import {
    Card,
    Typography,
    TextField,
    Button,
    Snackbar
} from "@material-ui/core";
import { connect } from "react-redux";
import {
    getLink,
    addLink,
    updateLinkStatus,
    deleteLink
} from "../../redux/actions/Link";
import MuiAlert from "@material-ui/lab/Alert";
import LinkEditModel from "./LinkComponent/linkEditModel";
import { Link } from "react-router-dom";
import Skeleton from "@material-ui/lab/Skeleton";
import copy from "copy-to-clipboard";
import { sidebar } from "../../redux/actions/header";
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

class LinkManage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            link: null,
            url: "",
            title: "",
            success: false,
            copy: false,
            copyText: "",
            errors1: false,
            errors2: false
        };
        this.handelSubmit = this.handelSubmit.bind(this);
        this.handelDelete = this.handelDelete.bind(this);
        this.copyToclip = this.copyToclip.bind(this);
    }
    componentDidMount() {
        this.props.getLink();
    }

    handelSubmit() {
        if (!this.state.url) {
            this.setState({
                errors1: true
            });
        } else if (!this.state.title) {
            this.setState({
                errors2: true
            });
        } else {
            const newData = {
                token: localStorage.getItem("auth-Token"),
                url: this.state.url,
                title: this.state.title
            };
            this.props.addLink(newData);

            setTimeout(
                () =>
                    this.setState({
                        url: "",
                        title: ""
                    }),
                1000
            );
        }
    }

    handelDelete(id) {
        const newData = { id: id, token: localStorage.getItem("auth-Token") };
        window.confirm("Are you sure you wish to delete this Link?") &&
            this.props.deleteLink(newData);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.Link !== null) {
            this.setState({
                link: nextProps.Link.Link
            });
        }
    }
    copyToclip(text) {
        copy(text);
        this.setState({
            copy: true,
            copyText: text
        });
    }

    render() {
        console.log(this.props);
        return (
            <div>
                <Snackbar
                    open={this.state.copy}
                    autoHideDuration={500}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() => {
                        this.setState({ copy: false });
                    }}
                >
                    <Alert
                        onClose={() => {
                            this.setState({ copy: false });
                        }}
                        severity="success"
                    >
                        {" "}
                        Token {this.state.copyText} copied !!
                    </Alert>
                </Snackbar>
                <Snackbar
                    open={this.props.Link.success}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() => this.props.updateLinkStatus()}
                >
                    <Alert
                        onClose={() => this.props.updateLinkStatus()}
                        severity="success"
                    >
                        {this.props.Link.message}
                    </Alert>
                </Snackbar>
                <Header />
                <div className="wrapper">
                    <SideBar />
                    <div
                        className="content-area overflow-hidden"
                        style={{ minHeight: "83vh" }}
                    >
                        <div className="page-header">
                            <h4 className="page-title">Manage Link</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="/console">Dashboard</Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    Manage Link
                                </li>
                            </ol>
                        </div>

                        <div className="row">
                            <div className="col-lg-5 mb-4">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="body1"
                                        className="px-2 py-2 h-12 bg-gradient-success font-bold text-white"
                                    >
                                        Manage Link
                                    </Typography>

                                    <div className="col-sm-12">
                                        <TextField
                                            fullWidth
                                            label="Link"
                                            className="mb-3"
                                            type="text"
                                            variant="standard"
                                            value={this.state.url}
                                            error={this.state.errors1}
                                            onChange={e =>
                                                this.setState({
                                                    url: e.target.value,
                                                    errors1: false
                                                })
                                            }
                                        />
                                        <TextField
                                            fullWidth
                                            label="Link Title"
                                            className="mb-3"
                                            type="text"
                                            autoComplete="off"
                                            variant="standard"
                                            value={this.state.title}
                                            error={this.state.errors2}
                                            onChange={e =>
                                                this.setState({
                                                    title: e.target.value,
                                                    errors2: false
                                                })
                                            }
                                        />
                                    </div>
                                    <div className="flex flex-end">
                                        <Button
                                            variant="contained"
                                            className=" mr-2 ml-2 mb-5"
                                            color="primary"
                                            onClick={() => this.handelSubmit()}
                                        >
                                            Add Link
                                        </Button>
                                    </div>
                                </Card>
                            </div>

                            <div className="col-lg-7 mb-4">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="body1"
                                        className="px-2 py-2 h-12 bg-gradient-success font-bold text-white"
                                    >
                                        All Link
                                    </Typography>

                                    <div className="col-sm-12">
                                        <div className="table-responsive">
                                            <table
                                                id="example1"
                                                className="table mb-0 table-striped"
                                            >
                                                <thead>
                                                    <tr>
                                                        <th>SL</th>
                                                        <th>Token</th>
                                                        <th>Link</th>
                                                        <th>Title</th>
                                                        <th>Action</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.link ===
                                                    null ? (
                                                        <tr>
                                                            <td colSpan={5}>
                                                                <Skeleton
                                                                    height={100}
                                                                    variant="text"
                                                                />
                                                            </td>
                                                        </tr>
                                                    ) : (
                                                        this.state.link.map(
                                                            (url, i) => (
                                                                <tr key={i}>
                                                                    <td>
                                                                        {i + 1}
                                                                    </td>
                                                                    <td
                                                                        onClick={() =>
                                                                            this.copyToclip(
                                                                                "{link-" +
                                                                                    (i +
                                                                                        1) +
                                                                                    "}"
                                                                            )
                                                                        }
                                                                    >
                                                                        {"{link-" +
                                                                            (i +
                                                                                1) +
                                                                            "}"}
                                                                    </td>
                                                                    <td>
                                                                        {
                                                                            url.url
                                                                        }
                                                                    </td>
                                                                    <td>
                                                                        {
                                                                            url.urltext
                                                                        }
                                                                    </td>
                                                                    <td className="flex flex-row flex-between">
                                                                        <div>
                                                                            <LinkEditModel
                                                                                data={
                                                                                    url
                                                                                }
                                                                            />
                                                                        </div>
                                                                        <div
                                                                            onClick={() =>
                                                                                this.handelDelete(
                                                                                    url._id
                                                                                )
                                                                            }
                                                                            className="icon mr-2 cursor-pointer text-xl"
                                                                        >
                                                                            <i className="fas fa-trash text-red" />
                                                                        </div>
                                                                    </td>
                                                                </tr>
                                                            )
                                                        )
                                                    )}
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </Card>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    Link: state.Link,
    side: state.side
});
export default connect(
    mapStateToProps,
    {
        getLink,
        addLink,
        updateLinkStatus,
        deleteLink,
        sidebar
    }
)(LinkManage);
