import Axios from "axios";
import {
    ALL_FOLLOWUP,
    EDIT_FOLLOWUP,
    FOLLOWUP_STATUS,
    ADD_FOLLOWUP
} from "./type";
import { API } from "../../Global";

export const getAllFollowup = () => dispatch => {
    Axios.post(API + "/allFollowup", {
        token: localStorage.getItem("auth-Token")
    })
        .then(res => {
            dispatch({
                type: ALL_FOLLOWUP,
                payload: res.data
            });
        })
        .catch(err => { });
};

export const editFollowup = newData => dispatch => {
    Axios.post(API + "/editFollowup", newData)
        .then(res => {
            dispatch({
                type: EDIT_FOLLOWUP,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const createFollowup = data => dispatch => {
    Axios.post(API + "/createFollowup", data)
        .then(res => {
            dispatch({
                type: ADD_FOLLOWUP,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const deleteFollowup = data => dispatch => {
    Axios.post(API + "/deleteFollowup", data)
        .then(res => {
            dispatch({
                type: ADD_FOLLOWUP,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const followupStatus = () => dispatch => {
    dispatch({
        type: FOLLOWUP_STATUS
    });
};
