import React, { Component } from 'react'
import Header from '../common/header'
import Sidebar from '../common/side'
import { Link, withRouter } from "react-router-dom"
import { Card, TextField, Button, Snackbar } from '@material-ui/core'
import MuiAlert from "@material-ui/lab/Alert";
import Axios from 'axios'
import { API } from "../../Global"
import { LogOUT } from "../../redux/actions/Login"
import { connect } from "react-redux"
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}
export class AccoutSetting extends Component {
    constructor(props) {
        super(props)
        this.state = {
            current: "",
            pass: "",
            repass: "",
            error: false,
            message: ""
        }
        this.handelChange = this.handelChange.bind(this)
    }

    handelChange() {
        if (this.state.current || this.state.pass || this.state.repass) {
            if (this.state.pass === this.state.repass) {
                const newData = {
                    token: localStorage.getItem('auth-Token'),
                    current: this.state.current,
                    pass: this.state.pass,
                    repass: this.state.repass
                }
                Axios.post(API + "/changePassword", newData).then(res => {
                    if (res.data.success) {
                        this.setState({
                            error: true,
                            message: res.data.message,
                            type: res.data.type

                        })
                        setTimeout(() => {
                            localStorage.removeItem("auth-Token");
                            localStorage.removeItem("expDate");

                            this.props.LogOUT()
                            this.props.history.push("/");
                        }, 3000);

                    } else {
                        this.setState({
                            error: true,
                            message: res.data.message,
                            type: res.data.type

                        })
                    }

                })
            } else {
                this.setState({
                    error: true,
                    message: "Password Dosn't match",
                    type: "error"
                })
            }
        } else {
            this.setState({
                error: true,
                message: "All Filds are Required",
                type: "warning"
            })
        }




    }

    render() {
        return (
            <div>
                <Snackbar
                    open={this.state.error}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() => this.setState({ error: false, message: "" })} >
                    <Alert
                        onClose={() => this.setState({ error: false, message: "" })}
                        severity={this.state.type} >{this.state.message}
                    </Alert>
                </Snackbar>
                <Header />
                <div className="wrapper">
                    <Sidebar />

                    <div className=" content-area overflow-hidden " style={{ minHeight: "83vh" }}>
                        <div className="page-header">
                            <h4 className="page-title">Account Setting</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="console">
                                        Dashboard
                                    </Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    Account Setting
                                </li>
                            </ol>
                        </div>



                        <Card
                            style={{
                                boxShadow:
                                    "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                            }}
                        >
                            <div className="flex flex-row justify-between bg-gradient-success items-center h-12">
                                <div
                                    color="textSecondary"
                                    variant="body1"
                                    className="  px-2 py-2  font-bold text-white"
                                >
                                    Change Password
                                </div>

                            </div>

                            <div className="col-lg-6 mb-4">
                                <TextField
                                    margin="dense"
                                    id="cpass"
                                    label="Current Password"
                                    type="password"
                                    fullWidth
                                    required
                                    onChange={(e) => this.setState({ current: e.target.value })}
                                />

                            </div>
                            <div className="col-lg-6 mb-4">
                                <TextField

                                    margin="dense"
                                    id="pass"
                                    label="Password"
                                    type="password"
                                    fullWidth
                                    required
                                    onChange={(e) => this.setState({ pass: e.target.value })}
                                />

                            </div>
                            <div className="col-lg-6 mb-4">
                                <TextField
                                    margin="dense"
                                    id="repass"
                                    label="Retype Password"
                                    type="password"
                                    fullWidth
                                    required
                                    onChange={(e) => this.setState({ repass: e.target.value })}
                                />

                            </div>
                            <div className="px-5 py-5">  <Button variant="contained" onClick={() => this.handelChange()} color="default" >Change Password</Button></div>

                        </Card>
                    </div>
                </div>
            </div>
        )
    }
}


export default withRouter(connect(null, { LogOUT })(AccoutSetting));
