import React, { Component } from "react";
import {
    DialogTitle,
    DialogContent,
    DialogContentText,
    Button,
    Dialog,
    DialogActions,
    Tooltip,
    Snackbar,
} from "@material-ui/core";
import ContactSupportIcon from "@material-ui/icons/ContactSupport";
import MuiAlert from "@material-ui/lab/Alert";

import copy from "copy-to-clipboard";
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}
class Token extends Component {
    constructor(props) {
        super(props);
        this.state = {
            model: false,
            copy: false,
            copyText: "",
        };
    }
    copyToclip(text) {
        copy(text);
        this.setState({
            copy: true,
            copyText: text,
        });
    }
    render() {
        return (
            <div>
                <Snackbar
                    open={this.state.copy}
                    autoHideDuration={500}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center",
                    }}
                    onClose={() => {
                        this.setState({ copy: false });
                    }}
                >
                    <Alert
                        onClose={() => {
                            this.setState({ copy: false });
                        }}
                        severity="success"
                    >
                        Token {this.state.copyText} copied !!
                    </Alert>
                </Snackbar>
                <Tooltip title="Token List" aria-label="Token List">
                    <Button
                        onClick={() => {
                            this.setState({ model: true });
                        }}
                        className=" mr-3"
                        color="inherit"
                        variant="contained"
                    >
                        <ContactSupportIcon />
                        Token
                    </Button>
                </Tooltip>
                <Dialog
                    maxWidth="xs"
                    open={this.state.model}
                    onClose={() => this.setState({ model: false })}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        {"Token List?"}
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            <p onClick={() => this.copyToclip("{fname}")}>
                                {"{fname}"} Massge will show email First name
                            </p>
                            <p onClick={() => this.copyToclip("{lname}")}>
                                {"{lname}"} Massge will show email Last name
                            </p>
                            <p onClick={() => this.copyToclip("{fullname}")}>
                                {"{fullname}"} Massage will show email full name
                            </p>
                            <p onClick={() => this.copyToclip("{email}")}>
                                {"{email}"} Massage will show email adress
                            </p>
                            <p onClick={() => this.copyToclip("{todayis}")}>
                                {"{todayis}"}Massage will show Today Name
                            </p>
                            <p onClick={() => this.copyToclip("{date}")}>
                                {"{date}"} Massage will show Today Date
                            </p>
                            <p onClick={() => this.copyToclip("{modelname}")}>
                                {"{modelname}"} Massage will show Model name
                            </p>
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            onClick={() => this.setState({ model: false })}
                            color="primary"
                        >
                            cancel
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}

export default Token;
