import {
    FOLLOWUP_STATUS,
    ADD_FOLLOWUP,
    ALL_FOLLOWUP,
    EDIT_FOLLOWUP,
    DELETE_FOLLOWUP
} from "../actions/type";
const initialState = {
    Followup: null,
    success: false,
    message: ""
};
// eslint-disable-next-line
export default function (state = initialState, actions) {
    switch (actions.type) {
        case ALL_FOLLOWUP:
            return {
                ...state,
                Followup: actions.payload
            };
        case ADD_FOLLOWUP:
            return {
                ...state,
                Followup: actions.payload.replydata,
                success: actions.payload.success,
                message: actions.payload.message
            };
        case EDIT_FOLLOWUP:
            return {
                ...state
            };
        case FOLLOWUP_STATUS:
            return {
                ...state,
                success: false,
                message: ""
            };
        case DELETE_FOLLOWUP:
            return {
                ...state
            };

        default:
            return state;
    }
}
