import {
    UPLOAD_IMAGE,
    LIST_IMG,
    DELETE_IMG,
    IMG_STATUS
} from "../actions/type";
const initialState = {
    Image: null,
    success: false,
    message: ""
};
// eslint-disable-next-line
export default function (state = initialState, actions) {
    switch (actions.type) {
        case LIST_IMG:
            return {
                ...state,
                Image: actions.payload
            };
        case UPLOAD_IMAGE:
            return {
                ...state,
                Image: actions.payload
            };
        case DELETE_IMG:
            return {
                ...state,
                Image: actions.payload.images,
                    success: actions.payload.success,
                    message: actions.payload.message

            };
        case IMG_STATUS:
            return {
                ...state,

                success: false,
                    message: ""

            };
        default:
            return state;
    }
}
