import React, { Component } from "react";
import Home from "./components/page/home.jsx";
import ManageCampaign from "./components/page/manageCampaign";
import { setCurrentUser } from "./redux/actions/Login";
import PopMail from "./components/page/popMail";
import CreateReply from "./components/page/createReply";
import AllReaply from "./components/page/allreply";
import Jwt from "jwt-decode";
import PrivateRoute from "./PrivateRoute";
import Login from "./components/page/Login";
import "core-js-pure/stable";
import "regenerator-runtime/runtime";
import { connect } from "react-redux";
import {
    BrowserRouter,
    Route,
    Switch,

} from "react-router-dom";
import ImageManage from "./components/page/ImageManagement";
import LinkManage from "./components/page/LinkManagement";
import Followup from "./components/page/followup";
import CreateFollowup from "./components/page/createFollowup";
import Payment from "./components/page/payment";
import AccoutSetting from "./components/page/accoutSetting";
import Trigger from "./components/page/trigger";
import AutoLogin from "./components/page/AutoLogin";
import Chat from "./components/page/Chat";
import ChatLinks from "./components/page/ChatLinkManagement";
class Main extends Component {

    componentDidMount() {
        const token = localStorage.getItem("auth-Token");
        if (token) {
            const decodedToken = Jwt(token);
            if (decodedToken.exp * 1000 < Date.now()) {
                localStorage.removeItem("auth-Token");
                this.props.history.push("/");
            } else {
                this.props.setCurrentUser();
            }
        }
    }

    render() {
        return (
            <BrowserRouter>
                <Switch>
                    <Route exact path={"/"} component={Login} />
                    <Route
                        exact
                        path={"/Admin/Login/:userId"}
                        component={AutoLogin}
                    />
                    <PrivateRoute exact path={"/console"} component={Home} />
                    <PrivateRoute
                        exact
                        path={"/ManageCampaign"}
                        component={ManageCampaign}
                    />
                    <PrivateRoute exact path={"/POP3"} component={PopMail} />
                    <PrivateRoute
                        exact
                        path={"/CreateReply"}
                        component={CreateReply}
                    />
                    <PrivateRoute
                        exact
                        path={"/AllReply"}
                        component={AllReaply}
                    />
                    <PrivateRoute
                        exact
                        path={"/Image"}
                        component={ImageManage}
                    />
                    <PrivateRoute exact path={"/Link"} component={LinkManage} />
                    <PrivateRoute
                        exact
                        path={"/AllFollowup"}
                        component={Followup}
                    />
                    <PrivateRoute
                        exact
                        path={"/CreateFollowup"}
                        component={CreateFollowup}
                    />
                    <PrivateRoute exact path={"/Payment"} component={Payment} />
                    <PrivateRoute
                        exact
                        path={"/Account"}
                        component={AccoutSetting}
                    />
                    <PrivateRoute exact path={"/Trigger"} component={Trigger} />
                    <PrivateRoute exact path={"/Chat"} component={Chat} />
                    <PrivateRoute
                        exact
                        path={"/ChatLinks"}
                        component={ChatLinks}
                    />
                </Switch>
            </BrowserRouter>
        );
    }
}

export default connect(
    null,
    {
        setCurrentUser
    }
)(Main);
