import { combineReducers } from "redux";
import totalleads from "./dashboardReducer";
import allreply from "./replyreducer";
import popmail from "./popReducer";
import Login from "./LoginReducer";
import getcam from "./campaignReducer";
import img from "./imageReducer";
import alllink from "./linkreducer";
import side from "./headerReducer";
import allfollowup from "./followupReducer";
import alltriggrt from "./triggerReducer";
import chatLinkssss from "./chatlinkreducers";
export default combineReducers({
    TotalLeads: totalleads,
    Reply: allreply,
    PopMail: popmail,
    isAuth: Login,
    campaign: getcam,
    Image: img,
    Link: alllink,
    CHATLINKS: chatLinkssss,
    side: side,
    Followup: allfollowup,
    Trigger: alltriggrt
});
