import {
    POP_MAIL,
    UPDATE_POP,
    UPDATE_POP_STATUS,
    EDIT_POP
} from "../actions/type";
const initialState = {
    PopMail: null,
    update: false,
    success: false
};
// eslint-disable-next-line
export default function(state = initialState, actions) {
    switch (actions.type) {
        case POP_MAIL:
            return {
                ...state,
                PopMail: actions.payload
            };
        case UPDATE_POP:
            return {
                ...state,
                PopMail: actions.payload,
                update: true
            };
        case EDIT_POP:
            return {
                ...state,
                PopMail: actions.payload,
                success: true
            };
        case UPDATE_POP_STATUS:
            return {
                ...state,
                update: false,
                success: false
            };
        // case EDIT_REPLY:
        //     let data = state.Reply;
        //     let thatReply = data.filter(id => id._id !== actions.payload._id);
        //     thatReply.push(actions.payload);
        //     thatReply.sort(function(a, b) {
        //         const keyA = a.replynum;
        //         const keyB = b.replynum;
        //         if (keyA < keyB) return -1;
        //         if (keyA > keyB) return 1;
        //         return 0;
        //     });

        //     return {
        //         ...state,
        //         Reply: thatReply,
        //         Editreply: true
        //     };
        // case REPLY_UPDATE_STATS:
        //     return {
        //         ...state,
        //         Editreply: false
        //     };

        default:
            return state;
    }
}
