import React, { Component, Fragment } from 'react';
import Header from "../common/header";
import SideBar from "../common/side";
import { Link } from "react-router-dom"
import { Card, Typography,  Button } from '@material-ui/core';
import { connect } from "react-redux"
import Skeleton from '@material-ui/lab/Skeleton';

import PaymentOption from "../common/Payment"

import {
    getCampaign,
    getPackage
} from "../../redux/actions/campaign";
// const paymenturl = "https://maillerpay.microxencloud.com/?order_id=1007&name=sdfg&address=fgsf&email=asdkfjghksdjf@aol.com&phone=gsdfg&price=15000&p_name=dfdf"
class Payment extends Component {
    constructor(props) {
        super(props)
        this.state = ({
            campaign: null,
            display: "none",
            loadButton: "show",
            package: null,
            model: false,
            show: true,
            methode: 0
        })
        this.handelPackage = this.handelPackage.bind(this)
        this.handelinfo = this.handelinfo.bind(this)
        this.sendData = this.sendData.bind(this)
        this.clickFunction = this.clickFunction.bind(this)
    }
    componentDidMount() {
        this.props.getCampaign()
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.campaign !== null) {
            this.setState({
                campaign: nextProps.campaign.campaign

            });
        }
        if (nextProps.campaign.package !== null) {
            this.setState({
                package: nextProps.campaign.package

            });
        }

    }
    handelPackage() {
        this.setState({
            display: "block",
            loadButton: "none"
        })
        const newData = { packageCat: this.state.campaign !== null && this.state.campaign.package_cat }
        this.state.campaign !== null && this.props.getPackage(newData)
    }
    clickFunction() {
        const newData = { packageCat: this.state.campaign !== null && this.state.campaign.package_cat }
        this.state.campaign !== null && this.props.getPackage(newData)
    }

    handelinfo(message) {
        var a = message.split("\n");
        return a;
    }

    sendData() {
        let data = [];
        if (this.state.campaign !== null) {
            data.push(this.state.data)
            return data
        }

    }

    render() {

        return (
            <div>
                <Header />
                <div className="wrapper">
                    <SideBar />

                    <div className=" content-area overflow-hidden " style={{ minHeight: "83vh" }}>
                        <div className="page-header">
                            <h4 className="page-title">Payment</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="console">
                                        Dashboard
                                    </Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    Payment
                                </li>
                            </ol>
                        </div>


                        <div className="row">
                            <div className="col-lg-12 mb-4">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <div className="flex flex-row justify-between bg-gradient-success items-center">
                                        <Typography
                                            color="textSecondary"
                                            gutterBottom
                                            variant="body1"
                                            className="px-2 py-2  font-bold text-white"
                                        >
                                            Price List
                                       </Typography>
                                        <PaymentOption action={this.clickFunction} package={this.state.package !== null && this.state.package} campData={this.state.campaign !== null && this.state.campaign} />
                                    </div>


                                    <div className="flex px-10 py-10 justify-center" style={{ display: this.state.loadButton }}>
                                        <Button
                                            onClick={() => this.handelPackage()}
                                            variant="contained" color="primary"> Load Package</Button>
                                    </div>
                                    <div style={{
                                        display: this.state.display
                                    }}>
                                        <div className="row px-5 py-5" style={{ backgroundColor: "#eceef9" }}>
                                            {this.state.package === null ? (
                                                <Fragment>
                                                    <div className="col-xs-6 col-sm-6 col-lg-3 mt-2">
                                                        <Skeleton variant="text" height={400} />
                                                    </div>
                                                    <div className="col-xs-6 col-sm-6 col-lg-3 mt-2">
                                                        <Skeleton variant="text" height={400} />
                                                    </div>
                                                    <div className="col-xs-6 col-sm-6 col-lg-3 mt-2">
                                                        <Skeleton variant="text" height={400} />
                                                    </div>
                                                    <div className="col-xs-6 col-sm-6 col-lg-3 mt-2">
                                                        <Skeleton variant="text" height={400} />
                                                    </div>
                                                </Fragment>



                                            ) : (
                                                    this.state.package.map((item, i) => (
                                                        <div
                                                            className="col-xs-6 col-sm-6 col-lg-3 mt-2"
                                                            key={item._id}

                                                        >
                                                            <div className="panel price  panel-color" style={{ padding: 0 }}>
                                                                <div className="panel-heading  text-center  bg-gradient-primary text-white">
                                                                    <h3>{item.name} </h3>
                                                                </div>
                                                                <div className="panel-body text-center">
                                                                    <p className="lead "><strong>{item.price} /</strong> month</p>
                                                                </div>
                                                                <ul className="list-group list-group-flush text-center">

                                                                    {this.handelinfo(item.decription).map(abc => (
                                                                        <li key={abc} className="list-group-item"
                                                                            dangerouslySetInnerHTML={{
                                                                                __html: abc

                                                                            }}
                                                                        ></li>
                                                                    ))}


                                                                </ul>
                                                                <div className="panel-footer">
                                                                    <div className="btn btn-lg btn-block  bg-gradient-success text-white">{this.state.campaign.package === item._id ? "Current Package" : "Upgrade Now"}</div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))

                                                )}

                                        </div>

                                    </div>


                                </Card>
                            </div>

                        </div>



                    </div>
                </div>
            </div >
        );
    }
}
const mapStateToProps = state => ({
    campaign: state.campaign
})

export default connect(mapStateToProps, { getCampaign, getPackage })(Payment)