import { decode } from "../../Helpers"

import {
    TRIGGER,
    ADD_TRIGGER,
    EDIT_TRIGGER,
    DELETE_TRIGGER,
    TRIGGER_STATUS
} from "../actions/type";
const initialState = {
    Trigger: null,
    success: false,
    message: "",
    type: ""
};
// eslint-disable-next-line
export default function (state = initialState, actions) {
    switch (actions.type) {
        case TRIGGER:
            return {
                ...state,
                Trigger: actions.payload
            };
        case ADD_TRIGGER:
            return {
                ...state,
                Trigger: decode(actions.payload.trigger),
                success: true,
                message: actions.payload.message,
                type: actions.payload.type

            };
        case TRIGGER_STATUS:
            return {
                ...state,
                success: false,
                message: "",
                type: ""
            };
        case EDIT_TRIGGER:
            return {
                ...state,
                Trigger: decode(actions.payload.trigger),
                success: true,
                message: actions.payload.message,
                type: actions.payload.type
            };
        case DELETE_TRIGGER:
            return {
                ...state,
                Trigger: decode(actions.payload.trigger),
                success: true,
                message: actions.payload.message,
                type: actions.payload.type
            };
        default:
            return state;
    }
}
