import Axios from "axios";
import { LOGIN_ACTION, SET_LOGIN_ERROR, GET_USER_DATA, LOGOUT } from "./type";
import { API } from "../../Global";

export const loginAction = newuser => dispatch => {
    Axios.post(`${API}/login`, newuser)
        .then(res => {
            localStorage.setItem("auth-Token", res.data.token);
            localStorage.setItem("expDate", res.data.c_info.campaignexp);
            localStorage.setItem("branding", res.data.c_info.branding);

            dispatch({
                type: LOGIN_ACTION,
                payload: res.data
            });
        })
        .catch(err => {
            dispatch({
                type: SET_LOGIN_ERROR,
                payload: err.response.data
            });
        });
};

export const autoLogin = newuser => dispatch => {
    Axios.post(`${API}/autologin`, newuser)
        .then(res => {
            localStorage.setItem("auth-Token", res.data.token);
            localStorage.setItem("expDate", res.data.c_info.campaignexp);
            localStorage.setItem("branding", res.data.c_info.branding);
            dispatch({
                type: LOGIN_ACTION,
                payload: res.data
            });
        })
        .catch(err => {
            dispatch({
                type: SET_LOGIN_ERROR,
                payload: err.response.data
            });
        });
};


export const getUserData = () => dispatch => {
    Axios.post(`${API}/loginValidate`, {
        token: localStorage.getItem("auth-Token")
    })
        .then(res => {
            dispatch({
                type: GET_USER_DATA,
                payload: res.data.data
            });
        })
        .catch(err => {
            dispatch({
                type: SET_LOGIN_ERROR,
                payload: err.response
            });
        });
};
export const LogOUT = () => {
    return {
        type: LOGOUT
    };
};

export const setCurrentUser = () => {
    return {
        type: LOGIN_ACTION
    };
};
