import React, { Component } from "react";
import Skeleton from "@material-ui/lab/Skeleton";
import { Typography, Tooltip, IconButton, Card } from "@material-ui/core";
import SyncIcon from "@material-ui/icons/Sync";
import { withRouter } from "react-router-dom";

class TodayTable extends Component {
   

    render() {
        const data = this.props.data;
        if (data.status !== undefined) {
            this.props.history.push("/console");
        }
        return (
            <div>
                <Card
                    style={{
                        boxShadow:
                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                    }}
                >
                    <div className="flex flex-row justify-between bg-gradient-success items-center">
                        <Typography
                            color="textSecondary"
                            gutterBottom
                            variant="body1"
                            className="px-2 py-2  font-bold text-white"
                        >
                            Today Statistics
                        </Typography>
                        <div>
                            <Tooltip
                                title="Refress Table"
                                aria-label="Refress Table"
                            >
                                <IconButton
                                    className="text-white justify-end   mr-3"
                                    color="secondary"
                                    onClick={() => this.props.action()}
                                >
                                    <SyncIcon />
                                </IconButton>
                            </Tooltip>
                        </div>
                    </div>

                    <div className="table-responsive">
                        <table className="table mb-0 table-striped">
                            <thead>
                                <tr>
                                    <th>Campaign</th>
                                    <th>Leads</th>
                                    <th>Reply</th>
                                    <th>FollowUp</th>
                                    <th>Complete</th>
                                </tr>
                            </thead>
                            <tbody>
                                {data ? (
                                    <tr>
                                        <td>{data.name} </td>
                                        <td>{data.Leads} </td>
                                        <td>{data.Reply} </td>
                                        <td>{data.Followup} </td>
                                        <td>{data.complete} </td>
                                    </tr>
                                ) : (
                                        <tr>
                                            <td colSpan={5}>
                                                <Skeleton
                                                    variant="text"
                                                    height={25}
                                                />
                                            </td>
                                        </tr>
                                    )}
                            </tbody>
                        </table>
                    </div>
                </Card>
            </div>
        );
    }
}

export default withRouter(TodayTable);
