import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";
import { withStyles } from "@material-ui/core/styles";
import {
    Card,
    Tooltip,
    Button
} from "@material-ui/core";
import { connect } from "react-redux";
import {
    getAllFollowup,
    editFollowup,
    followupStatus,
    deleteFollowup
} from "../../redux/actions/followup";
import Skeleton from "@material-ui/lab/Skeleton";
import EditFollowupModel from "./followupComponent/EditFollowupModel";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import { Link } from "react-router-dom";
import AddIcon from '@material-ui/icons/Add';
import { getLink } from "../../redux/actions/Link"
import { replaceAll } from "../../Helpers"

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}
class Followup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            reply: null,
            model: false,
            success: false,
            setReply: 0,
            id: null
        };
        this.editclick = this.editclick.bind(this);
        this.handelDelete = this.handelDelete.bind(this);
        this.replyPreview = this.replyPreview.bind(this)

    }
    componentDidMount() {
        this.props.getAllFollowup();
        this.props.getLink()
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.Followup.Followup !== null) {
            this.setState({
                reply: nextProps.Followup.Followup.replyData,
                setReply: parseInt(nextProps.Followup.Followup.setReply)
            });
        }
        if (nextProps.Link.Link !== null) {
            this.setState({
                links: nextProps.Link.Link

            });
        }
    }

    editclick(reply_id) {
        this.props.editFollowup(reply_id);
    }

    handelDelete(id) {
        const newData = {
            token: localStorage.getItem("auth-Token"),
            id: id
        };
        window.confirm("Are you sure you wish to delete this item?") &&
            this.props.deleteFollowup(newData);
    }
    replyPreview(message) {
        if (this.state.links !== null) {
            var links = [];
            var text = [];
            this.state.links.forEach((link, i) => {
                var linktext = "{link-" + (i + 1) + "}";
                var urlformat = "<a href='" + link.url + "'>" + link.urltext + "</a>";
                links.push(linktext);
                text.push(urlformat);
            });
            const msgs = replaceAll(message, links, text)

            return msgs;

        }


    }
    render() {
        return (
            <div>
                <Snackbar
                    open={this.props.Followup.success}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() => this.props.followupStatus()}
                >
                    <Alert
                        onClose={() => this.props.followupStatus()}
                        severity="success"
                    >
                        {this.props.Followup.message}
                    </Alert>
                </Snackbar>
                <Header />
                <div className="wrapper">
                    <SideBar />

                    <div className=" content-area overflow-hidden" style={{ minHeight: "83vh" }}>
                        <div className="page-header">
                            <h4 className="page-title">All Followup</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="console">
                                        Dashboard
                                    </Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    All Followup
                                </li>
                            </ol>
                        </div>
                        <Card
                            style={{
                                boxShadow:
                                    "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                            }}
                        >
                            <div className="flex flex-row justify-between bg-gradient-success items-center h-12">
                                <div
                                    color="textSecondary"
                                    variant="body1"
                                    className="  px-2 py-2  font-bold text-white"
                                >
                                    All Followup
                                </div>
                                <div>
                                    <Link to={"/CreateFollowup"}>
                                        <Tooltip
                                            title="Refress Table"
                                            aria-label="Refress Table"
                                        >
                                            <Button
                                                className="text-black  bg-white justify-end mr-3"
                                                color="default"
                                                variant="contained"

                                            >
                                                <AddIcon /> Followup
                                            </Button>
                                        </Tooltip>
                                    </Link>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12 table-responsive">
                                    <table id="example1" className="table mb-0 table-striped ">
                                        <thead>
                                            <tr>
                                                <th width="7%" >Serial</th>
                                                <th width="7%" >Interval</th>
                                                <th width="80%">Message Contents</th>
                                                <th width="5%">Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {this.state.reply === null ? (
                                                <tr>
                                                    <td colSpan={5}>
                                                        <Skeleton
                                                            variant="text"
                                                            height={50}
                                                        />
                                                    </td>
                                                </tr>
                                            ) : (
                                                    this.state.reply.map(replys => (
                                                        <tr key={replys._id}>
                                                            <td>
                                                                {replys.replynum}
                                                            </td>
                                                            <td>
                                                                {replys.interval}
                                                            </td>
                                                            <td>
                                                                <p
                                                                    dangerouslySetInnerHTML={{
                                                                        __html: this.replyPreview(replys.messagebody)

                                                                    }}
                                                                ></p>
                                                            </td>
                                                            <td className="flex flex-row flex-between">
                                                                <EditFollowupModel
                                                                    data={replys}
                                                                    setReply={
                                                                        this.state
                                                                            .setReply
                                                                    }
                                                                />

                                                                <div
                                                                    onClick={() => this.handelDelete(replys._id)}
                                                                    className="icon mr-2 cursor-pointer text-xl"
                                                                >
                                                                    <i className="fas fa-trash text-red"></i>
                                                                </div>


                                                            </td>
                                                        </tr>
                                                    ))
                                                )}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </Card>
                    </div>
                </div>
            </div >
        );
    }
}

const mapStateToProps = state => ({
    Followup: state.Followup,
    Link: state.Link
});

export default connect(mapStateToProps, {
    getAllFollowup,
    editFollowup,
    followupStatus,
    deleteFollowup,
    getLink,
})(withStyles(null)(Followup));
