import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import { withRouter } from "react-router-dom";
import Logo from "../assets/img/logo.png";
import Background from "../assets/img/bg.png";
import { connect } from "react-redux";
import { autoLogin } from "../../redux/actions/Login";
import MuiAlert from "@material-ui/lab/Alert";
import { Snackbar } from "@material-ui/core";
import { URL } from "../../Global";
import mobilelogo from "../assets/img/mainlogo.svg"
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const styles = theme => ({
    form: {
        width: "100%", // Fix IE 11 issue.
        marginTop: theme.spacing(1)
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    }
});

class AutoLogin extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: "",
            password: "",
            error: "",
            loginError: ""
        };
    }

    componentDidMount() {
        const newuser = {
            userID: this.props.match.params.userId,

        };
        this.props.autoLogin(newuser);
    }
    componentWillReceiveProps(nextProps) {

        if (nextProps.isAuth.isAuth === true) {
            this.props.history.push("/console");
        }
        if (nextProps.isAuth.error) {
            this.setState({
                loginError: nextProps.isAuth.error.message
            });
        }
    }
    render() {
        const { classes } = this.props;


        return (
            <div
                style={{
                    width: "100%",
                    display: "flex",
                    flexWrap: "wrap",
                    alignItems: "center",
                    padding: "15px",
                    minHeight: "100vh",
                    justifyContent: "center",
                    backgroundImage: `url(${URL}/public/${Background})`
                }}
            >
                <Snackbar
                    open={this.state.loginError ? true : false}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() =>
                        this.setState({
                            loginError: null
                        })
                    }
                >
                    <Alert
                        onClose={() =>
                            this.setState({
                                loginError: null
                            })
                        }
                        severity="error"
                    >
                        {this.state.loginError}
                    </Alert>
                </Snackbar>
                <CssBaseline />
                <div
                    className="lg:py-40 lg:px-24 xs:py-16 sm:py-16 xs:px-8 sm:px-8"
                    style={{
                        borderRadius: 10,
                        overflow: "hidden",

                        display: "flex",
                        background: "white",
                        maxWidth: "960px",
                        flexWrap: "wrap",
                        justifyContent: "space-between"
                    }}
                >
                    <div
                        className="xs:hidden sm:hidden lg:mr-10"
                        data-tilt
                        style={{
                            width: "316px"
                        }}
                    >
                        <div className="js-tilt">
                            <img
                                style={{
                                    verticalAlign: "middle"
                                }}
                                data-tilt
                                src={URL + "/public" + Logo}
                                alt="IMG"
                            />
                        </div>
                    </div>
                    <div style={{ width: "290px" }}>

                        <img
                            className="lg:hidden md:hidden"
                            style={{
                                verticalAlign: "middle",
                                paddingBottom: "30px"
                            }}
                            data-tilt
                            src={URL + "/public" + mobilelogo}
                            alt="IMG"
                        />
                        <Typography
                            component="h1"
                            className="text-center"
                            variant="h5"
                        >
                            Access Your Mail Server
                        </Typography>

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            disabled
                            label="Email Address"
                            onChange={e => {
                                this.setState({ email: e.target.value });
                            }}
                        />

                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            disabled
                            label="Password"
                            type="password"
                            onChange={e => {
                                this.setState({ password: e.target.value });
                            }}

                        />

                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            disabled
                            className={classes.submit}

                        >
                            Enter
                        </Button>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    isAuth: state.isAuth
});
export default withRouter(
    connect(mapStateToProps, { autoLogin })(withStyles(styles)(AutoLogin))
);
