import Axios from "axios";
import { LINK, ADD_LINK, EDIT_LINK, DELETE_LINK, LINK_STATUS } from "./type";
import { API } from "../../Global";
import { decode } from "../../Helpers";
export const getLink = () => dispatch => {
    Axios.post(API + "/getLink", { token: localStorage.getItem("auth-Token") })
        .then(res => {
            dispatch({
                type: LINK,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const addLink = data => dispatch => {
    Axios.post(API + "/addLink", data)
        .then(res => {
            dispatch({
                type: ADD_LINK,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const editLink = data => dispatch => {
    Axios.post(API + "/editLink", data)
        .then(res => {
            dispatch({
                type: EDIT_LINK,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const deleteLink = data => dispatch => {
    Axios.post(API + "/deletLink", data)
        .then(res => {
            dispatch({
                type: DELETE_LINK,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const updateLinkStatus = () => dispatch => {
    dispatch({
        type: LINK_STATUS
    });
};
