import Axios from "axios";
import { LIST_IMG, UPLOAD_IMAGE, DELETE_IMG, IMG_STATUS } from "./type";
import { API } from "../../Global";
import { decode } from "../../Helpers"
export const getImage = () => dispatch => {
    Axios.post(API + "/getimg", { token: localStorage.getItem("auth-Token") })
        .then(res => {
            dispatch({
                type: LIST_IMG,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const uploadimg = formData => dispatch => {
    Axios.post(API + "/uploadImage", formData)
        .then(res => {
            dispatch({
                type: UPLOAD_IMAGE,
                payload: decode(res.data)
            });
        })
        .catch(err => { });
};
export const deleteImg = data => dispatch => {
    Axios.post(API + "/deleteImg", data)
        .then(res => {
            dispatch({
                type: DELETE_IMG,
                payload: res.data
            });
        })
        .catch(err => { });
};
export const imgStatus = () => dispatch => {
    dispatch({
        type: IMG_STATUS
    });
};

