import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";
import {
    Card,
    Typography,
    TextField,
    Button,
    Snackbar
} from "@material-ui/core";
import { connect } from "react-redux";
import {
    getTrigger,
    addTrigger,
    updateTriggerStatus,
    deleteTrigger
} from "../../redux/actions/Trigger";
import MuiAlert from "@material-ui/lab/Alert";
import TriggerEditModel from "./TriggerComponent/TriggerEditModel";
import { Link } from "react-router-dom";
import Skeleton from "@material-ui/lab/Skeleton";
import copy from "copy-to-clipboard";
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

class Trigger extends Component {
    constructor(props) {
        super(props);
        this.state = {
            allTrigger: null,
            trigger: "",
            alttext: "",
            success: false,
            copy: false,
            copyText: '',
            errors1: false,
            errors2: false
        };
        this.handelSubmit = this.handelSubmit.bind(this);
        this.handelDelete = this.handelDelete.bind(this);
        this.copyToclip = this.copyToclip.bind(this);
    }
    componentDidMount() {
        this.props.getTrigger();
    }

    handelSubmit() {
        if (!this.state.trigger) {
            this.setState({
                errors1: true
            })

        } else if (!this.state.alttext) {
            this.setState({
                errors2: true
            })
        } else {
            const newData = {
                token: localStorage.getItem("auth-Token"),
                trigger: this.state.trigger,
                alttext: this.state.alttext
            };
            this.props.addTrigger(newData);

            setTimeout(
                () =>
                    this.setState({
                        trigger: "",
                        alttext: ""
                    }),
                1000
            );
        }
    }
    handelDelete(id) {
        const newData = { id: id, token: localStorage.getItem("auth-Token") };
        window.confirm("Are you sure you wish to delete this Link?") &&
            this.props.deleteTrigger(newData);

    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.Trigger !== null) {
            this.setState({
                allTrigger: nextProps.Trigger.Trigger
            });
        }
    }
    copyToclip(text) {
        copy(text);
        this.setState({
            copy: true,
            copyText: text
        })
    }
    render() {
        console.log(this.state)
        return (
            <div>
                <Snackbar
                    open={this.state.copy}
                    autoHideDuration={500}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() => {
                        this.setState({ copy: false });
                    }}>
                    <Alert
                        onClose={() => { this.setState({ copy: false }); }}
                        severity="success"> Token {this.state.copyText} copied !!
                    </Alert>
                </Snackbar>
                <Snackbar
                    open={this.props.Trigger.success}
                    autoHideDuration={6000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center"
                    }}
                    onClose={() => this.props.updateTriggerStatus()}
                >
                    <Alert
                        onClose={() => this.props.updateTriggerStatus()}
                        severity={this.props.Trigger.type}
                    >
                        {this.props.Trigger.message}
                    </Alert>
                </Snackbar>
                <Header />
                <div className="wrapper">
                    <SideBar />
                    <div className="content-area overflow-hidden" style={{ minHeight: "83vh" }}>
                        <div className="page-header">
                            <h4 className="page-title">Manage Trigger</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="/console">
                                        Dashboard
                                    </Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    Manage Trigger
                                </li>
                            </ol>
                        </div>

                        <div className="row">
                            <div className="col-lg-5 mb-4">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="body1"
                                        className="px-2 py-2 h-12 bg-gradient-success font-bold text-white"
                                    >
                                        Manage Trigger
                                    </Typography>

                                    <div className="col-sm-12">
                                        <TextField
                                            fullWidth
                                            label="Trigger"
                                            placeholder="{text}"
                                            className="mb-3"
                                            type="text"
                                            variant="standard"
                                            value={this.state.trigger}
                                            error={this.state.errors1}
                                            onChange={e =>
                                                this.setState({
                                                    trigger: e.target.value,
                                                    errors1: false
                                                })
                                            }
                                        />

                                        <TextField
                                            fullWidth
                                            label="Alternative Text"
                                            className="mb-3"
                                            type="text"
                                            autoComplete="off"
                                            variant="standard"
                                            value={this.state.alttext}
                                            error={this.state.errors2}
                                            onChange={e =>
                                                this.setState({
                                                    alttext: e.target.value,
                                                    errors2: false
                                                })
                                            }
                                        />
                                    </div>
                                    <div className="flex flex-end">
                                        <Button
                                            variant="contained"
                                            className=" mr-2 ml-2 mb-5"
                                            color="primary"
                                            onClick={() => this.handelSubmit()}
                                        >
                                            Add Trigger
                                        </Button>
                                    </div>
                                </Card>
                            </div>

                            <div className="col-lg-7 mb-4">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="body1"
                                        className="px-2 py-2 h-12 bg-gradient-success font-bold text-white"
                                    >
                                        All Trigger
                                    </Typography>

                                    <div className="col-sm-12">
                                        <div className="table-responsive">
                                            <table id="example1" className="table mb-0 table-striped">
                                                <thead>
                                                    <tr>
                                                        <th>SL</th>
                                                        <th>Trigger</th>
                                                        <th>Output Text</th>
                                                        <th>Action</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.allTrigger === null ? (
                                                        <tr>
                                                            <td colSpan={5}>
                                                                <Skeleton
                                                                    height={100}
                                                                    variant="text"
                                                                />
                                                            </td>
                                                        </tr>
                                                    ) : (
                                                            this.state.allTrigger.map(
                                                                (trigger, i) => (
                                                                    <tr key={i}>
                                                                        <td>{i + 1}</td>

                                                                        <td>
                                                                            {trigger.trigger}
                                                                        </td>
                                                                        <td>
                                                                            {trigger.alttext}
                                                                        </td>
                                                                        <td className="flex flex-row flex-between">
                                                                            <div>
                                                                                <TriggerEditModel
                                                                                    data={trigger}
                                                                                />
                                                                            </div>
                                                                            <div
                                                                                onClick={() =>
                                                                                    this.handelDelete(trigger._id)
                                                                                }
                                                                                className="icon mr-2 cursor-pointer text-xl"
                                                                            >
                                                                                <i className="fas fa-trash text-red"></i>
                                                                            </div>
                                                                        </td>
                                                                    </tr>
                                                                )
                                                            )
                                                        )}
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </Card>
                            </div>


                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    Trigger: state.Trigger
});
export default connect(mapStateToProps, {
    getTrigger,
    addTrigger,
    updateTriggerStatus,
    deleteTrigger
})(Trigger);
