import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";

import * as moment from "moment";
import { connect } from "react-redux";
import SyncIcon from "@material-ui/icons/Sync";

import { Card, Typography, Tooltip, IconButton } from "@material-ui/core";
import Skeleton from "@material-ui/lab/Skeleton";
import { Link } from "react-router-dom";
import { sidebar, getinfo } from "../../redux/actions/header";
import DateRangePicker from "react-bootstrap-daterangepicker";
import "bootstrap-daterangepicker/daterangepicker.css";
import Axios from "axios";
import { API } from "../../Global";

class Chat extends Component {
    constructor(props) {
        super(props);
        this.state = {
            domain: null,
            c_id: "",
            dateModel: false,
            reports: [],
            loading: false,
            paymentData: [],
            paymentLoading: false
        };
        this.handleCallback = this.handleCallback.bind(this);
        this.paymentReport = this.paymentReport.bind(this);
    }
    componentDidMount() {
        this.paymentReport();
    }

    paymentReport() {
        this.setState({ paymentLoading: true });
        Axios.post(API + "/getPaymentInfo", {
            token: localStorage.getItem("auth-Token")
        }).then(res => {
            this.setState({
                paymentData: res.data,
                paymentLoading: false
            });
        });
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.side.Info !== null) {
            this.setState({
                chat: nextProps.side.Info.chatEnable,
                domain: nextProps.side.Info.domain,
                c_id: nextProps.side.Info.c_id
            });
            this.handleCallback();
            if (nextProps.side.Info.chatEnable === 2) {
                this.props.history.push("/console");
            }
        }
    }
    handleCallback(start, end, label) {
        var startDate = moment(start).format("YYYY-MM-DD");
        var endDate = moment(end).format("YYYY-MM-DD");
        this.setState({ loading: true });
        Axios.defaults.headers.post["x-eflow-api-key"] =
            "7HMC04r4T4GEP8QKm29XOA";
        Axios.post("https://api.eflow.team/v1/affiliates/reporting/entity", {
            from: startDate,
            to: endDate,
            timezone_id: 80,
            currency_id: "USD",
            query: {
                filters: []
            },
            columns: [
                {
                    column: "sub1"
                },
                {
                    column: "sub2"
                },
                {
                    column: "date"
                },
                {
                    column: "offer"
                }
            ]
        }).then(res => {
            let host = window.location.host;
            let parts = host.split(".");
            let subdomain = "";
            if (parts.length >= 3) {
                subdomain = parts[0];
            }
            const data = res.data.table;
            if (data.length !== 0) {
                const result = data.filter(
                    sub =>
                        sub.columns[0].id === subdomain &&
                        sub.columns[1].id === this.state.c_id
                );

                this.setState({
                    reports: result,
                    loading: false
                });
            } else {
                this.setState({
                    reports: data,
                    loading: false
                });
            }
        });
    }

    render() {
        let host = window.location.host;
        let parts = host.split(".");
        let subdomain = "";
        if (parts.length >= 3) {
            subdomain = parts[0];
        }
        return (
            <div>
                <Header />
                <div className="wrapper">
                    <SideBar />
                    <div
                        className="content-area overflow-hidden"
                        style={{ minHeight: "83vh" }}
                    >
                        <div className="page-header">
                            <h4 className="page-title"> Chat Bot</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="/console">Dashboard</Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    Chat Bot
                                </li>
                            </ol>
                        </div>
                        <div className="row">
                            <div className="col-lg-12 pb-10">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="body1"
                                        className="px-2 h-12 py-2 bg-gradient-success font-bold text-white justify-center"
                                    >
                                        Chat Bot
                                    </Typography>

                                    <Typography
                                        color="textSecondary"
                                        gutterBottom
                                        variant="body1"
                                        className="px-2 h-12 py-2  font-bold text-black justify-center item-center"
                                    >
                                        https://{this.state.domain}/
                                        {subdomain.replace("console", "user")}/
                                        {this.state.c_id}
                                    </Typography>
                                </Card>
                            </div>

                            <div className="col-lg-12 ">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <div className="flex flex-row justify-between bg-gradient-success items-center">
                                        <Typography
                                            color="textSecondary"
                                            gutterBottom
                                            variant="body1"
                                            className="px-2 h-12 py-2  font-bold text-white justify-center"
                                        >
                                            Chat Bot Reports (
                                            <b className="text-black">
                                                Stats based on (GMT-04:00)
                                                America/New_York
                                            </b>
                                            )
                                        </Typography>
                                        <div>
                                            <Tooltip
                                                title="Refress Table"
                                                aria-label="Refress Table"
                                            >
                                                <IconButton
                                                    className="text-white justify-end   mr-3"
                                                    color="secondary"
                                                    onClick={() =>
                                                        this.handleCallback()
                                                    }
                                                >
                                                    <SyncIcon />
                                                </IconButton>
                                            </Tooltip>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <DateRangePicker
                                            initialSettings={{
                                                startDate: moment().format("L"),
                                                endDate: moment().format("L"),
                                                showDropdowns: true
                                            }}
                                            onCallback={this.handleCallback}
                                        >
                                            <input
                                                type="text"
                                                className="form-control"
                                            />
                                        </DateRangePicker>
                                    </div>

                                    <div className="table-responsive">
                                        <table className="table ">
                                            <thead>
                                                <tr>
                                                    <th>Date</th>
                                                    <th>Offer Name</th>
                                                    <th>Gross Click</th>
                                                    <th>Unique Click</th>
                                                    <th>Convertion</th>
                                                    <th>Payout</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {this.state.loading ? (
                                                    <tr>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                    </tr>
                                                ) : this.state.reports
                                                      .length === 0 ? (
                                                    <tr>
                                                        <td
                                                            colSpan="5"
                                                            style={{
                                                                textAlign:
                                                                    "center"
                                                            }}
                                                        >
                                                            No Data Available
                                                        </td>
                                                    </tr>
                                                ) : (
                                                    this.state.reports.map(
                                                        (report, i) => (
                                                            <tr key={i}>
                                                                <td>
                                                                    {moment
                                                                        .unix(
                                                                            report
                                                                                .columns[2]
                                                                                .id
                                                                        )
                                                                        .format(
                                                                            "DD MMM YYYY"
                                                                        )}
                                                                </td>
                                                                <td>
                                                                    {
                                                                        report
                                                                            .columns[3]
                                                                            .label
                                                                    }
                                                                </td>
                                                                <td>
                                                                    {report
                                                                        .reporting
                                                                        .duplicate_click +
                                                                        report
                                                                            .reporting
                                                                            .unique_click}
                                                                </td>
                                                                <td>
                                                                    {
                                                                        report
                                                                            .reporting
                                                                            .unique_click
                                                                    }
                                                                </td>
                                                                <td>
                                                                    {
                                                                        report
                                                                            .reporting
                                                                            .cv
                                                                    }
                                                                </td>
                                                                <td>
                                                                    ${" "}
                                                                    {
                                                                        report
                                                                            .reporting
                                                                            .revenue
                                                                    }
                                                                </td>
                                                            </tr>
                                                        )
                                                    )
                                                )}
                                            </tbody>
                                        </table>
                                    </div>
                                </Card>
                            </div>

                            <div className="col-lg-12 pt-10">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)"
                                    }}
                                >
                                    <div className="flex flex-row justify-between bg-gradient-success items-center">
                                        <Typography
                                            color="textSecondary"
                                            gutterBottom
                                            variant="body1"
                                            className="px-2 h-12 py-2  font-bold text-white justify-center"
                                        >
                                            Payment Information
                                        </Typography>
                                        <div>
                                            <Tooltip
                                                title="Refress Table"
                                                aria-label="Refress Table"
                                            >
                                                <IconButton
                                                    className="text-white justify-end   mr-3"
                                                    color="secondary"
                                                    onClick={() =>
                                                        this.paymentReport()
                                                    }
                                                >
                                                    <SyncIcon />
                                                </IconButton>
                                            </Tooltip>
                                        </div>
                                    </div>
                                    <div className="table-responsive">
                                        <table className="table ">
                                            <thead>
                                                <tr>
                                                    <th>Invoice ID</th>
                                                    <th>Date</th>
                                                    <th>Sale</th>
                                                    <th>Payout</th>
                                                    <th>Status</th>
                                                    <th>Note</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {this.state.paymentData
                                                    .length === 0 ? (
                                                    <tr>
                                                        <td
                                                            colSpan="6"
                                                            style={{
                                                                textAlign:
                                                                    "center"
                                                            }}
                                                        >
                                                            {" "}
                                                            No Data Available
                                                        </td>
                                                    </tr>
                                                ) : this.state
                                                      .paymentLoading ? (
                                                    <tr>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                        <td>
                                                            <Skeleton variant="rect" />
                                                        </td>
                                                    </tr>
                                                ) : (
                                                    this.state.paymentData.map(
                                                        (info, i) => (
                                                            <tr key={i}>
                                                                <td>
                                                                    {
                                                                        info.invoice
                                                                    }
                                                                </td>
                                                                <td>
                                                                    {
                                                                        info.date_from
                                                                    }{" "}
                                                                    -{" "}
                                                                    {
                                                                        info.date_to
                                                                    }
                                                                </td>
                                                                <td>
                                                                    {
                                                                        info.sale_count
                                                                    }
                                                                </td>
                                                                <td>
                                                                    {
                                                                        info.amount
                                                                    }
                                                                </td>
                                                                <td className="text-capitalize">
                                                                    {
                                                                        info.status
                                                                    }
                                                                </td>
                                                                <td>
                                                                    {
                                                                        info.public_note
                                                                    }
                                                                </td>
                                                            </tr>
                                                        )
                                                    )
                                                )}
                                            </tbody>
                                        </table>
                                    </div>
                                </Card>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    side: state.side
});

export default connect(
    mapStateToProps,
    { sidebar, getinfo }
)(Chat);
