import React, { Component } from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route } from "react-router-dom";
import App from "./Main";
import { Provider } from "react-redux";
import store from "./store";
import reportWebVitals from './reportWebVitals';
import Axios from "axios";
import {API} from "./Global"
ReactDOM.render(
    <Provider store={store}>
        <Router>
            <Route path="/" component={App} />
        </Router>
    </Provider>,
    document.getElementById("root")
);
class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            footer:""
        }
    } 
    componentDidMount() {
        Axios.post(API+"/branding", {
            id: localStorage.getItem("branding")
        }).then(res => {
            console.log(res.data)
            this.setState({footer:res.data})
        })
    }
    render() {
        return (
            <footer class="footer" >
                <div class="container">
                    <div class="row align-items-center flex-row-reverse">
                        <div class="col-md-12 col-sm-12 mt-3 mt-lg-0 text-center" id="contact">
                            <p dangerouslySetInnerHTML={{ __html: this.state.footer.body }}></p>
                        </div>
                    </div>
                </div>
            </footer>
        );
    }
}

export default Footer;

ReactDOM.render(<Footer />, document.getElementById('footer'));
reportWebVitals();