import Axios from "axios";
import {
    CHAT_LINK,
    CHAT_ADD_LINK,
    CHAT_EDIT_LINK,
    CHAT_DELETE_LINK,
    CHAT_LINK_STATUS
} from "./type";
import { API } from "../../Global";
import { decode } from "../../Helpers";
export const getLink = () => dispatch => {
    Axios.post(API + "/getChatLink", {
        token: localStorage.getItem("auth-Token")
    })
        .then(res => {
            dispatch({
                type: CHAT_LINK,
                payload: decode(res.data)
            });
        })
        .catch(err => {});
};
export const addLink = data => dispatch => {
    Axios.post(API + "/addChatLink", data)
        .then(res => {
            dispatch({
                type: CHAT_ADD_LINK,
                payload: decode(res.data)
            });
        })
        .catch(err => {});
};
export const editLink = data => dispatch => {
    Axios.post(API + "/editChatLink", data)
        .then(res => {
            dispatch({
                type: CHAT_EDIT_LINK,
                payload: decode(res.data)
            });
        })
        .catch(err => {});
};
export const deleteLink = data => dispatch => {
    Axios.post(API + "/deletChatLink", data)
        .then(res => {
            dispatch({
                type: CHAT_DELETE_LINK,
                payload: decode(res.data)
            });
        })
        .catch(err => {});
};
export const updateLinkStatus = () => dispatch => {
    dispatch({
        type: CHAT_LINK_STATUS
    });
};
