import React, { Component } from "react";
import Header from "../common/header";
import SideBar from "../common/side";
import {  DropzoneDialog } from "material-ui-dropzone";
import {
    Card,
    GridListTile,
    GridListTileBar,
    Snackbar,
    Button,
    Tooltip,
    LinearProgress,
} from "@material-ui/core";
import { connect } from "react-redux";
import {
    getImage,
    uploadimg,
    deleteImg,
    imgStatus,
} from "../../redux/actions/Image";
import GridList from "@material-ui/core/GridList";
import copy from "copy-to-clipboard";
import IconButton from "@material-ui/core/IconButton";
import MuiAlert from "@material-ui/lab/Alert";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import Skeleton from "@material-ui/lab/Skeleton";
import { Link } from "react-router-dom";
import AddIcon from "@material-ui/icons/Add";

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}
class ImageManage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            files: [],
            images: null,
            copy: false,
            copyText: "",
            open: false,
            upload: false
        };
        this.handelUpload = this.handelUpload.bind(this);
        this.copyToclip = this.copyToclip.bind(this);
        this.handelDelete = this.handelDelete.bind(this);
    }
    handleChange(files) {
        this.setState({
            files: files,
            upload: true
        });

        setTimeout(() => this.handelUpload(), 1000);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.Image !== null) {
            this.setState({
                images: nextProps.Image.Image,
                upload: false
            });
        } else {
            this.setState({
                upload: true
            });
        }
    }
    handelUpload() {
        const formData = new FormData();
        const imagefile = Array.from(this.state.files);
        imagefile.forEach((file, i) => {
            formData.append("photos[]", file);
        });
        formData.append("token", localStorage.getItem("auth-Token"));

        this.props.uploadimg(formData);
        this.setState({
            files: [],
            open: false,
        });
    }
    componentDidMount() {
        this.props.getImage();
    }

    copyToclip(text) {
        copy(text);
        this.setState({
            copy: true,
            copyText: text,
        });
    }

    handelDelete(id) {
        const newData = {
            token: localStorage.getItem("auth-Token"),
            id: id,
        };
        window.confirm("Are you sure you wish to delete this Image?") &&
            this.props.deleteImg(newData);
    }

    render() {

        return (
            <div>
                <Snackbar
                    open={this.state.copy}
                    autoHideDuration={2000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center",
                    }}
                    onClose={() => {
                        this.setState({ copy: false });
                    }}
                >
                    <Alert
                        onClose={() => { this.setState({ copy: false }); }}
                        severity="success"
                    >

                        Token {this.state.copyText} copied !!
                    </Alert>
                </Snackbar>

                <Snackbar
                    open={this.props.Image.success}
                    autoHideDuration={2000}
                    anchorOrigin={{
                        vertical: "top",
                        horizontal: "center",
                    }}
                    onClose={() => {
                        this.props.imgStatus();
                    }}
                >
                    <Alert
                        onClose={() => {
                            this.props.imgStatus();
                        }}
                        severity="success"
                    >
                        {this.props.Image.message}
                    </Alert>
                </Snackbar>

                <Header />
                <div className="wrapper">
                    <SideBar />
                    <div
                        className="content-area overflow-hidden"
                        style={{ minHeight: "83vh" }}
                    >
                        <div className="page-header">
                            <h4 className="page-title">Upload Image</h4>
                            <ol className="breadcrumb sm:hidden xs:hidden">
                                <li className="breadcrumb-item">
                                    <Link to="/console">Dashboard</Link>
                                </li>
                                <li
                                    className="breadcrumb-item active"
                                    aria-current="page"
                                >
                                    Image Upload
                                </li>
                            </ol>
                        </div>
                        <div className="row">
                            <div className="col-lg-12 mb-4">
                                <Card
                                    style={{
                                        boxShadow:
                                            "0 0 0 1px rgba(61, 119, 180, .12), 0 8px 16px 0 rgba(91,139,199,.24)",
                                    }}
                                >
                                    <div className="flex flex-row justify-between bg-gradient-success items-center h-12">
                                        <div
                                            color="textSecondary"
                                            variant="body1"
                                            className="  px-2 py-2  font-bold text-white"
                                        >
                                            Upload Image
                                        </div>
                                        <div>
                                            <Tooltip
                                                title="Upload Image"
                                                aria-label="Upload Image"
                                            >
                                                <Button
                                                    className="text-black  bg-white justify-end mr-3"
                                                    color="default"
                                                    variant="contained"
                                                    onClick={() =>
                                                        this.setState({
                                                            open: true,
                                                        })
                                                    }
                                                >
                                                    <AddIcon /> Upload Image
                                                </Button>
                                            </Tooltip>
                                        </div>
                                    </div>
                                    <div className="px-3 py-3">
                                        <DropzoneDialog
                                            open={this.state.open}
                                            onSave={this.handleChange.bind(
                                                this
                                            )}
                                            showAlerts={false}
                                            // showPreviewsInDropzone={false}
                                            filesLimit={50}
                                            onClose={() =>
                                                this.setState({ open: false })
                                            }
                                            acceptedFiles={[
                                                "image/jpeg",
                                                "image/png",
                                                "image/bmp",
                                            ]}
                                        />
                                    </div>
                                    <div>
                                        {this.state.upload && <LinearProgress />}

                                        <GridList
                                            style={{ width: "100%", height: 450, }}
                                            className="flex flex-between ml-1"  >
                                            {this.state.images === null ? (
                                                <Skeleton variant="rect" height={100} />
                                            ) : (
                                                    this.state.images.map((image, i) => (
                                                        <GridListTile
                                                            key={i}
                                                            style={{ width: "25%", }}  >
                                                            <img
                                                                height="100%"
                                                                width="100%"
                                                                alt="MicroXen Mailer"
                                                                src={window.link + "/uploads/" + image.panel + "/" + image.filename}
                                                            />

                                                            <GridListTileBar
                                                                title={"{img-" + (i + 1) + "}"}
                                                                subtitle={
                                                                    <div
                                                                        className="cursor-pointer"
                                                                        onClick={() =>
                                                                            this.copyToclip("{img-" + (i + 1) + "}")
                                                                        }
                                                                    >
                                                                        Click To Copy
                                                                    </div>
                                                                }
                                                                actionIcon={
                                                                    <IconButton
                                                                        onClick={() => this.handelDelete(image._id)}
                                                                        aria-label={"bal"}
                                                                        style={{ color: "red", backgroundColor: "#C8C8C8", }}
                                                                    >
                                                                        <DeleteOutlineIcon />
                                                                    </IconButton>
                                                                }
                                                            />
                                                        </GridListTile>
                                                    )
                                                    )
                                                )}
                                        </GridList>
                                    </div>
                                </Card>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    Image: state.Image,
});

export default connect(mapStateToProps, {
    getImage,
    uploadimg,
    deleteImg,
    imgStatus,
})(ImageManage);
