import React, { Component } from "react";
import {
    DialogTitle,
    DialogContent,
    DialogContentText,
    TextField,
    Button,
    Dialog,
    DialogActions,
    FormControl,
    MenuItem,
    FormHelperText,
    InputLabel,
    Select
} from "@material-ui/core";
import CKEditor from "ckeditor4-react";
import { connect } from "react-redux";
import { editFollowup } from "../../../redux/actions/followup";
import Token from "../../common/token";
class EditFollowupModel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            model: false,
            replynum: "",
            messagebody: "",
            interval: 0,
            id: "",
            replyAllow: 0,
            errors: null
        };
    }
    componentDidMount() {
        this.setState({
            messagebody: this.props.data.messagebody,
            replynum: this.props.data.replynum,
            interval: this.props.data.interval,
            id: this.props.data._id,
            replyAllow: this.props.setReply
        });
    }
    HandelSubmit() {
        if (!this.state.interval) {
            this.setState({
                errors: "Required"
            })

        } else {
            const newData = {
                token: localStorage.getItem("auth-Token"),
                id: this.state.id,
                replynum: this.state.replynum,
                messagebody: this.state.messagebody,
                interval: parseInt(this.state.interval)
            };
            this.props.editFollowup(newData);
            this.setState({ model: false });
        }
    }

    render() {
        let array = [];
        const allow = () => {
            for (let index = 1; index <= this.state.replyAllow; index++) {
                array.push(
                    <MenuItem key={index} value={index}>
                        <em>{index}</em>
                    </MenuItem>
                );
            }
            return array;
        };
        return (
            <div>
                <div
                    onClick={() => {
                        this.setState({ model: true });
                    }}
                    className="icon mr-2 cursor-pointer text-xl"
                >
                    <i className="fas fa-edit "></i>
                </div>
                <Dialog
                    disableEnforceFocus
                    fullWidth={true}
                    maxWidth="lg"
                    open={this.state.model}
                    onClose={() => this.setState({ model: false })}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        {"Do you Want to change this Followup Message ?"}
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            <div class="row ml-2">
                                <div className="col-sm-6">
                                    <FormControl fullWidth={true}>
                                        <InputLabel id="demo-simple-select-helper-label">
                                            Order by
                                        </InputLabel>
                                        <Select
                                            labelId="demo-simple-select-helper-label"
                                            id="demo-simple-select-helper"
                                            value={
                                                this.state.replynum !== null &&
                                                this.state.replynum
                                            }
                                            onChange={e =>
                                                this.setState({
                                                    replynum: e.target.value
                                                })
                                            }
                                        >
                                            {allow()}
                                        </Select>
                                        <FormHelperText>
                                            Your Replay Number
                                        </FormHelperText>
                                    </FormControl>
                                </div>

                                <div className="col-sm-6">
                                    <TextField
                                        id="filled-email-input"
                                        label="Delay time (min)"
                                        className="mb-3"
                                        type="number"
                                        value={
                                            this.state.interval !== null &&
                                            this.state.interval
                                        }
                                        error={this.state.errors === null ? false : true}
                                        autoComplete="off"
                                        variant="standard"
                                        onChange={e =>
                                            this.setState({
                                                interval: e.target.value,
                                                errors: null
                                            })
                                        }
                                    />
                                </div>
                            </div>
                            <div className="px-4 py-2">
                                <Token />
                            </div>
                            <div className="col-sm-12 ml-2">
                                <div className="form-group">
                                    <label for="groupname">Content</label>
                                    <CKEditor
                                        onChange={e =>
                                            this.setState({
                                                messagebody: e.editor.getData()
                                            })
                                        }
                                        data={
                                            this.state.messagebody !== null &&
                                            this.state.messagebody
                                        }
                                    />
                                </div>
                            </div>
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            variant="contained"
                            onClick={() => this.setState({ model: false })}
                            color="secondary"
                        >
                            cancel
                        </Button>
                        <Button
                            variant="contained"
                            onClick={() => this.HandelSubmit()}
                            color="primary"
                        >
                            Save Change
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}

export default connect(null, { editFollowup })(EditFollowupModel);
