import {
    LINK,
    ADD_LINK,
    EDIT_LINK,
    DELETE_LINK,
    LINK_STATUS
} from "../actions/type";
const initialState = {
    Link: null,
    success: false,
    message: ""
};
// eslint-disable-next-line
export default function(state = initialState, actions) {
    switch (actions.type) {
        case LINK:
            return {
                ...state,
                Link: actions.payload
            };
        case ADD_LINK:
            return {
                ...state,
                Link: actions.payload,
                success: true,
                message: "Link Added"
            };
        case LINK_STATUS:
            return {
                ...state,
                success: false,
                message: ""
            };
        case EDIT_LINK:
            return {
                ...state,
                Link: actions.payload,
                success: true,
                message: "Link Updated"
            };
        case DELETE_LINK:
            return {
                ...state,
                Link: actions.payload,
                success: true,
                message: "Link Deleted"
            };
        default:
            return state;
    }
}
